<template>
    <div><!--  -->
        <div>
            <el-breadcrumb separator="/">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <el-card v-if="!showApply">
            <el-row>
                <el-col :span="4" :offset="20" v-if="user.type === 0">
                    <el-button icon="el-icon-s-claim" type="warning" @click="showApply = true"></el-button>
                    <el-button icon="el-icon-s-comment" type="info" @click="showHistory = true"
                        v-if="!showHistory"></el-button>
                </el-col>
                <el-col :span="2" :offset="22" v-else>
                    <el-button icon="el-icon-s-comment" type="info" @click="showHistory = true"
                        v-if="!showHistory"></el-button>
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="logList" v-if="!showHistory">
                <el-table-column label="">
                    <template slot-scope="scope">
                        <div style="white-space: pre-wrap;">{{ scope.row.content }}</div>
                    </template>
                </el-table-column>
                <el-table-column prop="createName" label=""></el-table-column>
                <el-table-column prop="createTime" label=""></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="primary" @click="handleCheckMessage(scope.row)" round></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <div v-if="showHistory">
                <el-table :data="historyLogList">
                    <el-table-column label="" width="1200">
                        <template slot-scope="scope">
                            <div style="white-space: pre-wrap;">{{ scope.row.content }}</div>
                        </template>
                    </el-table-column>
                    <el-table-column prop="createName" label=""></el-table-column>
                    <el-table-column prop="createTime" label=""></el-table-column>
                </el-table>
                <!--  -->
                <el-pagination style="margin-top: 10px;text-align: left;" @size-change="handleSizeChange"
                    @current-change="handleCurrentChange" :current-page.sync="queryObj.page"
                    :page-sizes="[8, 10, 15, 20, 30]" :page-size="queryObj.pageSize"
                    layout="total, sizes, prev, pager, next" :total="total">
                </el-pagination>
                <el-button style="margin-top: 10px;" type="primary" icon="el-icon-back"
                    @click="showHistory = false"></el-button>
            </div>
        </el-card>

        <el-card v-if="showApply">
            <el-row>
                <el-col :span="2" :offset="22">
                    <el-button icon="el-icon-chat-dot-round" type="warning" @click="showApply = false"></el-button>
                </el-col>
            </el-row>
            <h3></h3>
            <el-table :data="applyCaseSubList">
                <el-table-column prop="caseName" label=""></el-table-column>
                <el-table-column prop="subName" label=""></el-table-column>
                <el-table-column prop="description" label=""></el-table-column>
                <el-table-column prop="createTime" label=""></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="danger" size="medium" round @click="removeCaseSubApply(scope.row.id)">
                            </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <hr>
            <h3></h3>
            <el-table :data="applyTaskList">
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tag type="warning" effect="dark" v-if="scope.row.type === 1"></el-tag>
                        <el-tag type="primary" effect="dark" v-else></el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="description" label=""></el-table-column>
                <el-table-column prop="planDays" label=""></el-table-column>
                <el-table-column prop="createTime" label=""></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="danger" size="medium" round @click="removeTaskApply(scope.row.id)"> </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <hr>
            <h3></h3>
            <el-table :data="delayList">
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tag type="success" effect="dark" v-if="scope.row.caseSubId"></el-tag>
                        <el-tag type="warning" effect="dark" v-if="scope.row.type === 1"></el-tag>
                        <el-tag type="primary" effect="dark" v-if="scope.row.type === 2"></el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="description" label=""></el-table-column>
                <el-table-column prop="applyDays" label=""></el-table-column>
                <el-table-column prop="predictTime" label=""></el-table-column>
                <el-table-column prop="applyReason" label=""></el-table-column>
                <el-table-column prop="delayType" label=""></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="danger" size="medium" round @click="removeDelayApply(scope.row.id)"> </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <hr>
            <h3></h3>
            <el-table :data="finishList">
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tag type="success" effect="dark" v-if="scope.row.caseSubId"></el-tag>
                        <el-tag type="warning" effect="dark" v-if="scope.row.type === 1"></el-tag>
                        <el-tag type="primary" effect="dark" v-if="scope.row.type === 2"></el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="description" label=""></el-table-column>
                <el-table-column prop="applyTime" label=""></el-table-column>
                <el-table-column prop="createTime" label=""></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="danger" size="medium" round @click="removeFinishApply(scope.row.id)">
                            </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <hr>
        </el-card>
    </div>
</template>

<script>
import { getHistoryLogByUserId } from '@/api/log'
import { mapActions, mapState } from 'vuex'
import { checkingApplyCaseSub, removeCaseSubApply } from '@/api/applyCaseSub'
import { checkingApplyTask, removeTaskApply } from '@/api/applyTask'
import { getDelayListByUserId, removeDelayApply } from '@/api/caseDelayApply'
import { getFinishListByUserId, removeFinishApply } from '@/api/caseFinishApply'
export default {
    data() {
        return {
            showApply: false,
            showHistory: false,
            queryObj: {
                page: 1,
                pageSize: 8
            },
            applyCaseSubList: [],
            applyTaskList: [],
            delayList: [],
            finishList: [],
            historyLogList: [],
            total: null,
        }
    },
    created() {
        // 
        if (this.user.type === 0)
            this.getAllList()
        this.getLogWithMe()
    },
    computed: {
        ...mapState('log', ['logList']),
        ...mapState(['user'])
    },
    methods: {
        ...mapActions('log', ['getLogList', 'checkMessage']),
        // 
        async getLogWithMe() {
            this.queryObj.userId = this.user.id
            const res = await getHistoryLogByUserId(this.queryObj)
            if (res.code === 200) {
                this.historyLogList = res.data.records
                this.total = res.data.total
            }

        },
        handleSizeChange(val) {
            this.queryObj.pageSize = val
            this.queryObj.page = 1
            this.getLogWithMe()
        },
        handleCurrentChange(val) {
            this.queryObj.page = val
            this.getLogWithMe()
        },
        async handleCheckMessage(row) {
            const res = await this.checkMessage(row)
            this.$message.success(res.data)
            this.getLogList()
        },
        async getAllList() {
            var res = await checkingApplyCaseSub(this.user.id)
            if (res.code === 200) {
                this.applyCaseSubList = res.data
            } else {
                this.$message.error(res.msg)
                return
            }

            // 
            res = await checkingApplyTask(this.user.id)
            if (res.code === 200) {
                this.applyTaskList = res.data
            } else {
                this.$message.error(res.msg)
                return
            }

            // 
            res = await getDelayListByUserId(this.user.id)
            if (res.code === 200) {
                this.delayList = res.data
            } else {
                this.$message.error(res.msg)
                return
            }

            //
            res = await getFinishListByUserId(this.user.id)
            if (res.code === 200) {
                this.finishList = res.data
            } else {
                this.$message.error(res.msg)
                return
            }

        },
        //
        removeFinishApply(id) {
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {
                var res = await removeFinishApply(id)
                if (res.code === 200) {
                    this.$message.success(res.data)
                    this.getAllList()
                } else {
                    this.$message.error(res.msg)
                }
            })
        },
        // 
        removeDelayApply(id) {
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {
                var res = await removeDelayApply(id)
                if (res.code === 200) {
                    this.$message.success(res.data)
                    this.getAllList()
                } else {
                    this.$message.error(res.msg)
                }
            })
        },
        // 
        removeTaskApply(id) {
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {
                var res = await removeTaskApply(id)
                if (res.code === 200) {
                    this.$message.success(res.data)
                    this.getAllList()
                } else {
                    this.$message.error(res.msg)
                }
            })
        },
        // 
        removeCaseSubApply(id) {
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {
                var res = await removeCaseSubApply(id)
                if (res.code === 200) {
                    this.$message.success(res.data)
                    this.getAllList()
                } else {
                    this.$message.error(res.msg)
                }
            })
        }
    }
}
</script>

<style scoped></style>