<template>
    <div>
        <!--  -->
        <div>
            <el-breadcrumb separator="/">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <el-card v-if="checkHistoryVisible === false">
            <div class="btns">
                <el-row>
                    <el-col :span="4" :offset="9">
                        <h1></h1>
                    </el-col>
                    <el-col :span="2" :offset="9">
                        <el-button type="info" icon="el-icon-s-order" @click="openHistory()">  </el-button>
                    </el-col>
                </el-row>
            </div>
            <el-table :data="delayList" style="width: 100%">
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tag effect="dark" type="success" v-if="scope.row.caseSubId !== null"></el-tag>
                        <el-tag effect="dark" v-else-if="scope.row.type === 2"></el-tag>
                        <el-tag effect="dark" type="warning" v-else-if="scope.row.type === 1"></el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="description" label="">
                </el-table-column>
                <el-table-column prop="delayType" label="">
                </el-table-column>
                <el-table-column label="" width="250">
                    <template slot-scope="scope">
                        <div style="white-space: pre-wrap;">{{ scope.row.applyReason }}</div>
                    </template>
                </el-table-column>
                <el-table-column prop="applyDays" label="">
                </el-table-column>
                <el-table-column prop="formatPredictTime" label="">
                </el-table-column>
                <el-table-column prop="applyName" label="">
                </el-table-column>
                <el-table-column prop="applyTime" label="">
                </el-table-column>
                <el-table-column label="">

                    <template slot-scope="scope">
                        <el-button type="success" @click="handleCheck(scope.row, 1)"></el-button>
                        <el-button type="primary" @click="handleCheck(scope.row, 2)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <!--  -->
        <el-card v-if="checkHistoryVisible">
            <el-page-header @back="checkHistoryVisible = false" content=""></el-page-header>
            <el-table :data="checkedApplyList" style="width: 100%">
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tag effect="dark" type="success" v-if="scope.row.status === 1"></el-tag>
                        <el-tag effect="dark" type="danger" v-else-if="scope.row.status === 2"></el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="rejectReason" label="">
                </el-table-column>
                <el-table-column prop="description" label="">
                </el-table-column>
                <el-table-column prop="applyName" label="">
                </el-table-column>
                <el-table-column prop="delayType" label="">
                </el-table-column>
                <el-table-column prop="applyReason" label="">
                </el-table-column>
                <el-table-column prop="applyDays" label="">
                </el-table-column>
                <el-table-column prop="predictTime" label="">
                </el-table-column>
                <el-table-column prop="createTime" label="">
                </el-table-column>
                <el-table-column prop="checkName" label="" width="80">
                </el-table-column>
                <el-table-column prop="checkTime" label="">
                </el-table-column>
            </el-table>
            <el-pagination style="margin-top: 10px;" @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page="queryInfo.page" :page-sizes="[5, 8, 10, 15]" :page-size="queryInfo.pageSize"
                layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
import { getApplyList, judgeApply, delayHistory } from '@/api/caseDelayApply'
import { mapActions, mapMutations, mapState } from 'vuex'
import { formatDate } from '@/utils/common'
import { format } from 'echarts'
export default {
    data() {
        return {
            //
            checkHistoryVisible: false,
            queryInfo: {
                page: 1,
                pageSize: 5
            },
            total: 0,
            //
            checkedApplyList: []
        }
    },
    computed: {
        ...mapState('apply',['delayList']),
        ...mapState(['user'])
    },
    created() {
        const pageSize = +localStorage.getItem("pim_check_delay_pageSize")
        this.queryInfo.pageSize = pageSize === 0 ? 5 : pageSize
        this.getDelay()
    },
    methods: {
        ...mapActions('apply',['getDelay']),
        async handleCheck(row, status) {
            row.status = status
            row.checkUser = this.user.id
            if (status === 2) {
                this.$prompt('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                }).then(async ({ value }) => {
                    if (value === '' || value === null) {
                        this.$message.error("")
                        return 0
                    }
                    row.rejectReason = value
                    const res = await judgeApply(row)
                    if (res.code === 200) {
                        this.$message.success("")
                        this.getDelay()
                    } else
                        this.$message.error(res.msg)
                }).catch(() => {
                    //
                })
            } else {
                this.$prompt('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    inputValue: row.applyDays,
                    inputPattern: /[1-9]\d?/,
                    inputErrorMessage: ',0'
                }).then(async ({ value }) => {
                    row.applyDays = +value
                    const res = await judgeApply(row)
                    if (res.code === 200) {
                        this.$message.success("")
                        this.getDelay()
                    } else
                        this.$message.error(res.msg)
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            }
        },
        openHistory() {
            this.checkHistoryVisible = true
            this.checkHistory()
        },
        async checkHistory() {
            const res = await delayHistory(this.queryInfo)
            this.total = res.data.total
            this.checkedApplyList = res.data.records
            this.checkedApplyList.forEach(element => {
                element.predictTime = formatDate(element.predictTime)
            });
        },
        handleSizeChange(value) {
            localStorage.setItem("pim_check_delay_pageSize", value)
            this.queryInfo.pageSize = value
            this.queryInfo.page = 1
            this.checkHistory()
        },
        handleCurrentChange(value) {
            this.queryInfo.page = value
            this.checkHistory()
        }
    }
}
</script>

<style scoped></style>