<template>
    <el-card>
        <div class="subInfo">
            <el-page-header @back="$router.back()" :content="caseName"></el-page-header>
            <div class="table">
                <div class="subTable">
                    <el-table :data="subInfo" stripe border @cell-dblclick="handleDoubleClick">

                        <el-table-column type="expand">
                            <template slot-scope="scope">
                                <el-row v-for="(item, index) in scope.row.chargeName" :key="item">
                                    <el-col :span="4">
                                        <el-tag class="chargeNameTag" closable 
                                        @close="removeDirector(scope.row.chargeId,index)"
                                        @click="getCaseByUserName(scope.row.chargeId[index],item)">
                                            {{ item }}
                                        </el-tag>
                                    </el-col>
                                </el-row>

                            </template>
                        </el-table-column>

                        <el-table-column prop="subName" label="" width="120">
                        </el-table-column>

                        <el-table-column prop="number" label="" width="100">
                        </el-table-column>

                        <el-table-column prop="level" label="" width="50">
                        </el-table-column>

                        <el-table-column prop="startTime" label="" width="100">
                        </el-table-column>

                        <!-- + -->
                        <el-table-column prop="targetTime" label="" width="110">
                        </el-table-column>

                        <!-- ++ -->
                        <el-table-column prop="standardTime" label="" width="110">
                        </el-table-column>

                        <!--  -->
                        <el-table-column prop="ideaTime" label="" width="110">
                        </el-table-column>

                        <el-table-column prop="finishTime" label="" width="110">
                        </el-table-column>

                        <el-table-column prop="planDays" label="" width="110">
                        </el-table-column>

                        <el-table-column prop="unforcedDays" label="" width="110">
                        </el-table-column>

                        <el-table-column prop="applyDelay" label="" width="110">
                        </el-table-column>

                        <el-table-column prop="status" label="" width="100"
                            :filters="[{ text: '', value: '' }, { text: '', value: '' }, { text: '', value: '' }, { text: '', value: '' }]"
                            :filter-method="filterTag" filter-placement="bottom-end">
                            <template slot-scope="scope">
                                <el-tag :type="showtype(scope.row.status)" disable-transitions>{{
                                    number2status(scope.row.status)
                                }}</el-tag>
                            </template>

                        </el-table-column>

                        <el-table-column label="" width="180">
                            <template slot-scope="scope">
                                <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                                    <el-button type="info" size="mini" icon="el-icon-info" round
                                        @click="openCommitView(scope.row)"></el-button>
                                </el-tooltip>
                                <el-tooltip effect="dark" content="" placement="top" :enterable="false"
                                    v-if="scope.row.startTime === null">
                                    <el-button type="success" size="mini" icon="el-icon-success" round
                                        @click="openCommitView(scope.row)"></el-button>
                                </el-tooltip>
                                <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                                    <el-button type="primary" size="mini" icon="el-icon-edit" round
                                        @click="openCommitView(scope.row)"></el-button>
                                </el-tooltip>
                            </template>
                        </el-table-column>
                    </el-table>
                    <!--  -->

                </div>
                <!--  -->
                <el-drawer :visible.sync="drawer" :direction="direction" size="50%">
                    <div class="userCase" border>
                        <div class="chargeInfo">
                            <label class="chargeName">{{ chargeName }}</label>
                        </div>
                        <el-table :data="userInfo">
                            <el-table-column prop="caseName" label="" width="245">
                            </el-table-column>
                            <el-table-column prop="subName" label="" width="80">
                            </el-table-column>
                            <el-table-column prop="startTime" label="" width="100">
                            </el-table-column>
                            <el-table-column prop="presetTime" label="" width="110">
                            </el-table-column>
                            <el-table-column prop="deadLine" label="/" width="110">
                            </el-table-column>
                            <el-table-column prop="status" label="" width="100"
                                :filters="[{ text: '', value: '' }, { text: '', value: '' }, { text: '', value: '' }, { text: '', value: '' }]"
                                :filter-method="filterTag" filter-placement="bottom-end">
                                <template slot-scope="scope">
                                    <el-tag :type="showtype(scope.row.status)" disable-transitions>{{
                                        number2status(scope.row.status)
                                    }}</el-tag>
                                </template>
                            </el-table-column>
                        </el-table>
                    </div>
                </el-drawer>
                <!--  -->
                <el-drawer :visible.sync="delayDrawer" :direction="direction" size="50%">
                    <el-table :data="delayList" style="width: 100%" border>
                        <el-table-column prop="caseName" label="" width="240">
                        </el-table-column>
                        <el-table-column prop="subName" label="" width="100">
                        </el-table-column>
                        <el-table-column prop="type" label="" width="120">
                        </el-table-column>
                        <el-table-column prop="applyReason" label="">
                        </el-table-column>
                        <el-table-column prop="applyDays" label="" width="80">
                        </el-table-column>
                        <el-table-column prop="predictTime" label="" width="120">
                        </el-table-column>
                    </el-table>
                </el-drawer>
                <!--  -->
                <el-dialog title="" :visible.sync="commitVisible" width="30%" @close="closeCommitDialog">
                    <el-form ref="commitFormRef" :model="commitForm" :rules="rules">
                        <!--  -->
                        <el-form-item label=''>
                            <el-form-item :label="commitForm.subName"></el-form-item>
                        </el-form-item>
                        <el-form-item label="">
                        </el-form-item>
                        <!--  -->
                        <el-form-item>
                            <el-card class="box-card">
                                <div v-for="o in commitForm.content" :key="o" class="text item">
                                    {{ o }}
                                </div>
                                <label v-if="commitForm.content.length === 0"></label>
                            </el-card>
                        </el-form-item>
                        <!--  -->
                        <el-form-item prop="newContent" label=":"
                            v-if="user.type === 1 || chargeId.indexOf(user.id) !== -1">
                            <el-input type="textarea" v-model="commitForm.newContent"></el-input>
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button type="primary" @click="submitCommitForm"
                            v-if="user.type === 1 || chargeId.indexOf(user.id) !== -1"></el-button>
                        <el-button @click="commitVisible = false"> </el-button>
                    </span>
                </el-dialog>
                <!--  -->
            </div>
        </div>
    </el-card>
</template>

<script>
import { formatDate, getStatus } from '@/utils/common'
import { getSubByUserId } from '@/api/caseSub'
import {removeDirector} from '@/api/caseSubUser'
import { getDelayByStatus } from '@/api/caseDelayApply'
import { getById, saveCommit } from '@/api/caseSubCommit';
import { mapState } from 'vuex';
export default {

    props: {
        subInfo: Array,
        caseName: String
    },
    created() {
        // this.initData()
    },
    data() {
        return {
            drawer: false,
            direction: 'rtl',
            // 
            delayDrawer: false,
            delayList: [],
            //
            userInfo: [],
            chargeName: '',
            // 
            commitVisible: false,
            commitForm: {
                content: [],
                newContent: null,
                chargeId: []
            },
            rules: {
                newContent: [
                    {
                        required: true, message: '', trigger: 'blur'
                    },
                    {
                        min: 8, max: 255, message: '8255', trigger: 'blur'
                    }
                ]
            }
        }
    },
    computed: {
        ...mapState(['user'])
    },
    methods: {
        //
        async handleDoubleClick(row, column) {
            if (column.label === '') {
                this.getUnforcedDays(row)
            } else if (column.label === '')
                this.getApplyDelay(row)

        },
        async getUnforcedDays(row) {
            var res = await getDelayByStatus({ caseSubId: row.id, status: 1, type: '' })
            this.delayList = res.data
            this.delayList.map(item => item.predictTime = formatDate(item.predictTime))
            this.delayDrawer = true
        },
        async getApplyDelay(row) {
            var res = await getDelayByStatus({ caseSubId: row.id, status: 1, type: '' })
            this.delayList = res.data
            this.delayList.map(item => item.predictTime = formatDate(item.predictTime))
            this.delayDrawer = true
        },
        async getCaseByUserName(id,name) {
            var res = await getSubByUserId(id)
            this.userInfo = res.data
            //
            this.chargeName = name
            for (var i = 0; i < this.userInfo.length; i++) {
                this.userInfo[i].startTime = formatDate(this.userInfo[i].startTime)
                const presetTime = new Date(this.userInfo[i].startTime)
                this.userInfo[i].presetTime = formatDate(presetTime.setDate(presetTime.getDate() + this.userInfo[i].planDays))
                this.userInfo[i].status = getStatus(this.userInfo[i].startTime, this.userInfo[i].presetTime, this.userInfo[i].finishTime)
                this.userInfo[i].deadLine = Math.floor(this.userInfo[i].status == 0 ? (new Date(this.userInfo[i].presetTime) - new Date().setHours(0, 0, 0, 0)) / (1000 * 3600 * 24) :
                    (new Date(new Date().setHours(0, 0, 0, 0) - new Date(this.userInfo[i].presetTime))) / (1000 * 3600 * 24))
            }
            this.drawer = true
        },
        filterTag(value, row) {
            return row.status === value;
        },
        showtype(tag) {
            if (tag === 0)
                return "primary"
            else if (tag === 1)
                return "success"
            else if (tag === 2)
                return "danger"
            else if (tag === 3)
                return "warning"
        },

        number2status(status) {
            if (status === 0)
                return ""
            else if (status === 1)
                return ""
            else if (status === 2)
                return ""
            else if (status === 3)
                return ""
            else if (status === 4)
                return ""
        },
        async openCommitView(caseSub) {
            this.commitVisible = true
            this.commitForm.subName = caseSub.subName
            this.commitForm.caseSubId = caseSub.id
            // 
            var res = await getById(caseSub.id)
            res = res.data
            //
            this.commitForm.content = []
            for (var i = 0; i < res.length; i++) {
                this.commitForm.content.push(res[i].content)
            }
        },
        async submitCommitForm() {
            //
            this.$refs.commitFormRef.validate(async valid => {
                if (!valid) return;
                const commmitObj = {}
                commmitObj.caseSubId = this.commitForm.caseSubId
                commmitObj.content = this.commitForm.newContent
                commmitObj.createUser = this.user.id
                var res = await saveCommit(commmitObj)
                this.$message(res.data)
                //
                this.commitVisible = false
            })

        },
        closeCommitDialog() {
            //
            this.$refs.commitFormRef.resetFields()
        },
        //
        async removeDirector(ids,index) {
            if(ids.length===1){
                this.$message.warning('')
                return
            }
            const res = await removeDirector(ids[index])
            if(res.code===200){
                this.$message.success(res.data)
            }else{
                this.$message.error(res.msg)
            }
        },
    }
}
</script>

<style scoped>
.el-page-header {
    margin-bottom: 10px;
}



.chargeName {
    font-size: 50px;
    color: white;
}

.chargeNameTag {
    margin: 7px;
    font-size: 20px;
}
</style>