<template>
    <div class="indexTable">
        <div class="query" style="margin-bottom: 15px;">
            <el-row :gutter="20">
                <!--  -->
                <el-col :span="4">
                    <el-input class="queryText" placeholder="" v-model="queryText" clearable @change="handleQuery">
                    </el-input>
                </el-col>

                <!--  -->
                <el-col :span="4">
                    <el-select v-model="queryStatus" placeholder="" clearable @change="handleQuery">
                        <el-option v-for="item in levels" :key="item" :label="item" :value="item">
                        </el-option>
                    </el-select>
                </el-col>

                <el-col :span="2">
                    <!--  -->
                    <el-button type="primary" @click="handleQuery"> <i class="el-icon-search"></i></el-button>
                </el-col>

                <el-col :span="2">
                    <!--  -->
                    <el-button type="primary" @click="handleReset"> <i class="el-icon-s-tools"></i></el-button>
                </el-col>

            </el-row>
        </div>

        <el-table :data="pageInfo" border stripe max-height=600 :default-sort="{ prop: 'doingDay', order: 'descending' }">
            <el-table-column type="index" label="" width="50">
            </el-table-column>
            <el-table-column prop="name" label="" width="260">
            </el-table-column>
            <el-table-column prop="level" label="" >
            </el-table-column>
            <el-table-column prop="directorName" label="" >
            </el-table-column>
            <el-table-column prop="startTime" label="" >
            </el-table-column>
            <el-table-column prop="presetTime" label="" >
            </el-table-column>
            <el-table-column prop="finishTime" label="" >
            </el-table-column>
            <el-table-column prop="planDay" label="" >
            </el-table-column>
            <el-table-column prop="doingDay" label="" >
            </el-table-column>
            <el-table-column prop="unforcedDay" label="" >
            </el-table-column>
            <el-table-column prop="curStage" label="" >
            </el-table-column>
            <el-table-column prop="status" label="" >
                <template slot-scope="scope">
                    <el-tag effect="dark" :type="showtype(scope.row.status)" disable-transitions>{{ number2status(scope.row.status)
                    }}</el-tag>
                </template>

            </el-table-column>
            <el-table-column label="" width="120">
                <template slot-scope="scope">
                    <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                        <el-button type="info" size="mini" icon="el-icon-info" round
                            @click="showSub(scope.row)"></el-button>
                    </el-tooltip>
                    <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                        <el-button type="danger" size="mini" icon="el-icon-s-custom" round
                            @click="showPerson(scope.row)"></el-button>
                    </el-tooltip>
                </template>

            </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination style="margin-top: 10px;text-align: left;" @size-change="handleSizeChange"
            @current-change="handleCurrentChange" :current-page.sync="page" :page-sizes="[5, 9,10, 20]" :page-size="size"
            layout="total, sizes, prev, pager, next" :total="total">
        </el-pagination>
    </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex';

export default {
    data() {
        return {
            pageInfo: [],
            page: 1,
            size: 9,
            total: 0,
            queryText: '',
            caseInfo: [],
            levels: ['', '', '', '', ''],
            queryStatus: '',
            queryStartTime: '',
            queryEndTime: '',
            timeOptions: {
                shortcuts: [{
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        var start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        const year = start.getFullYear();
                        const month = (start.getMonth() + 1).toString().padStart(2, '0');
                        start = new Date(`${year}-${month}-01`)
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        var start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                        const year = start.getFullYear();
                        const month = (start.getMonth() + 1).toString().padStart(2, '0');
                        start = new Date(`${year}-${month}-01`)
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        var start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 180);
                        const year = start.getFullYear();
                        const month = (start.getMonth() + 1).toString().padStart(2, '0');
                        start = new Date(`${year}-${month}-01`)
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '',
                    onClick(picker) {
                        const end = new Date();
                        var start = new Date();
                        start = start.getFullYear() + "-01-01"
                        start = new Date(start)
                        picker.$emit('pick', [start, end]);
                    }
                }]
            }
        }
    },
    computed: {
        ...mapState('caseM', ['caseList', 'queryList'])
    },
    created() {
        var pageSize=+localStorage.getItem('pim_caseTable_pageSize')
        this.pageSize=pageSize===0?10:pageSize
        this.caseInfo = this.caseList
        this.getTableDate()
    },
    watch: {
        queryList: {
            handler() {
                this.caseInfo = this.queryList
            }
        },
        caseList: {
            handler() {
                this.caseInfo = this.caseList
            }
        },
        caseInfo: {
            handler() {
                this.getTableDate()
            }
        }
    },
    methods: {
        ...mapMutations('caseM', ['queryCase']),
        showSub(row) {
            this.$router.push({
                name: 'case-sub',
                query: {
                    caseId: row.id,
                    caseName: row.name
                }
            })
        },
        //
        showPerson(row){
            this.$router.push({
                name: 'case-person',
                query: {
                    caseId: row.id,
                    caseName: row.name
                }
            })
        },
        showtype(tag) {
            if (tag === 0)
                return "primary"
            else if (tag === 1)
                return "success"
            else if (tag === 2)
                return "danger"
            else if (tag === 3)
                return "warning"
        },
        number2status(status) {
            if (status === 0)
                return ""
            else if (status === 1)
                return ""
            else if (status === 2)
                return ""
            else if (status === 3)
                return ""
            else if (status === 4)
                return ""
            else if (status === 5)
                return ""
        },
        transformStatus(status) {
            if (status === "")
                return 0
            else if (status === "")
                return 1
            else if (status === "")
                return 2
            else if (status === "")
                return 3
            else if (status === "")
                return 4
            else if (status === "")
                return 5
        },
        //
        handleQuery() {
            //1. 
            //2. 
            //4. 
            //5. 
            var queryObj = {}
            if (this.queryText !== '')
                queryObj.name = this.queryText
            // 
            if (this.queryStatus !== '' && this.queryStatus !== null) {
                // this.queryStatus = this.transformStatus(this.queryStatus)
                queryObj.status = this.transformStatus(this.queryStatus)
            }

            //
            if (this.queryStartTime !== null && this.queryStartTime !== '')
                queryObj.startTime = this.queryStartTime

            //
            if (this.queryEndTime !== null && this.queryEndTime !== '')
                queryObj.endTime = this.queryEndTime


            this.queryCase(queryObj)

            this.page = 1
            // this.caseInfo = this.queryList
        },
        handleReset() {
            //queryList
            this.queryCase({})
            //
            this.queryText = ''
            this.queryLevel = null
        },
        //
        handleSizeChange(val) {
            localStorage.setItem('pim_caseTable_pageSize',val)
            this.size = val
            this.page = 1
            this.getTableDate()
        },
        //
        handleCurrentChange(val) {
            this.page = val
            this.getTableDate()
        },
        getTableDate() {
            this.pageInfo = this.caseInfo.slice((this.page - 1) * this.size,
                this.page * this.size)
            this.total = this.caseInfo.length
        }
    }
}
</script>

<style scoped></style>