<template>
    <div>
        <!--  -->
        <div>
            <el-breadcrumb separator="/">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <el-card>
            <div>
                <el-row>
                    <el-col :span="1" :offset="8">
                        <h1><i class="el-icon-error" style="color:red"></i></h1>
                    </el-col>
                    <el-col :span="4" :offset="0">
                        <h1 style="text-align: center;"></h1>
                    </el-col>
                </el-row>


            </div>
            <el-table :data="subInfo" style="font-size: 15px;" :highlight-current-row="true">
                <el-table-column prop="caseName" label="">
                </el-table-column>
                <el-table-column prop="subName" label="">
                </el-table-column>

                <el-table-column prop="level" label="">
                </el-table-column>

                <el-table-column prop="planDays" label="">
                </el-table-column>

                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tooltip effect="dark" content="" placement="top" :enterable="false"
                            v-if="user.type === 1 && scope.row.startTime === null">
                            <el-button type="success" size="mini" icon="el-icon-video-play" round
                                @click="launch(scope.row)"></el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" content="" placement="right-start">
                            <el-button icon="el-icon-s-promotion" type="primary" size="mini" round
                                @click="openCaseDetail(scope.row)">
                            </el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="setDirectorVisible" width="30%">
            <el-row>
                <el-col :span="12">
                    <el-select v-model="directors" multiple filterable placeholder="">
                        <el-option-group v-for="group in allUser" :key="group.value" :label="group.label">
                            <el-option v-for="item in group.children" :key="item.id" :label="item.name" :value="item.id">
                            </el-option>
                        </el-option-group>
                    </el-select>
                </el-col>
                <el-col :span="12">
                    <el-date-picker v-model="launchTime" type="date" placeholder=""
                        value-format="yyyy-MM-dd HH:mm:ss">
                    </el-date-picker>
                </el-col>
            </el-row>
            <span slot="footer" class="dialog-footer">
                <el-button @click="setDirectorVisible = false"> </el-button>
                <!-- <el-button type="danger" @click="launchSub(curCaseSub)"></el-button> -->
                <el-button type="primary" @click="setDirectorFn()"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { exceptionSub } from '@/api/caseSub'
import { getUserListWithAssistants } from '@/api/user'
import { updateCaseSub, startOrFinish } from '@/api/caseSub'
import { formatDate } from '@/utils/common'
import { setDirector, countUser } from '@/api/caseSubUser'
import { mapState } from 'vuex'
export default {
    name: 'checkException',
    data() {
        return {
            subInfo: [],
            setDirectorVisible: false,
            directors: [],
            curCaseSub: null,
            //
            allUser: [
                {
                    value: 0,
                    label: '',
                    children: []
                },
                {
                    value: 1,
                    label: '',
                    children: []
                },
                {
                    value: 2,
                    label: 'IE',
                    children: []
                }
            ],
            // 
            launchTime: '',
        }
    },
    created() {
        this.exceptionSub()
        this.getAllUser()
    },
    computed: {
        ...mapState(['user'])
    },
    methods: {
        async exceptionSub() {
            const res = await exceptionSub()
            this.subInfo = res.data
        },
        //
        async launch(row) {
            this.initSetDirector(row.id)
        },
        //
        async initSetDirector(id) {
            // 
            const res = await countUser(id)
            //
            this.directors = res.data.map(item => item.userId)
            //
            this.setDirectorVisible = true
            //id
            this.curCaseSub = id
        },
        //
        async getAllUser() {
            const res = await getUserListWithAssistants()
            if (res.code === 200) {
                this.editUser = res.data
                res.data.forEach((item) => {
                    this.allUser[item.status].children.push(item)
                })
            } else
                this.$message.error(res.msg)
        },
        // 
        async launchSub(id) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {
                //
                var res = await startOrFinish({ id: id, startTime: this.launchTime })
                if (res.code === 200) {
                    this.$message.success(res.data)
                    this.launchTime = ''
                    this.setDirectorVisible = false
                    this.exceptionSub(this.caseId)
                } else {
                    this.$message.error(res.msg)
                }
            }).catch(()=>{
                // 
                this.launchTime = ''
            })
        },
        //
        async setDirectorFn() {
            if (this.directors.length === 0) {
                this.$message.error('')
                return
            }
            if (this.launchTime === '') {
                this.$message.error('')
                return
            }
            const res = await setDirector({ id: this.curCaseSub, chargeId: this.directors })
            if (res.code === 200) {
                this.$message.success(res.data)
                this.setDirectorVisible = false
                //
                this.launchSub(this.curCaseSub)
            } else
                this.$message.error(res.msg)
        },
        //
        openCaseDetail(row) {
            this.$router.push({
                name: '',
                query: {
                    caseId: row.caseId,
                    caseName: row.caseName
                }
            })
        }
    }
}
</script>

<style scoped></style>