<template>
    <div>
        <!--  -->
        <div>
            <el-breadcrumb separator="/">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <el-card v-if="checkHistoryVisible === false">
            <div class="btns">
                <el-row>
                    <el-col :span="4" :offset="9">
                        <h1></h1>
                    </el-col>
                    <el-col :span="2" :offset="9">
                        <el-button type="info" icon="el-icon-s-order" @click="openHistory()">  </el-button>
                    </el-col>
                </el-row>
            </div>
            <el-table :data="finishList" style="width: 100%" @cell-dblclick="handleDoubleClick"
            :cell-class-name="cellClassName">
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tag effect="dark" type="success" v-if="scope.row.caseSubId !== null"></el-tag>
                        <el-tag effect="dark" v-else-if="scope.row.type === 2"></el-tag>
                        <el-tag effect="dark" type="warning" v-else-if="scope.row.type === 1"></el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="description" label="">
                </el-table-column>
                <el-table-column prop="startTime" label="">
                </el-table-column>
                <el-table-column prop="applyTime" label="">
                </el-table-column>
                <el-table-column prop="estimateValue" label="" width="80">
                </el-table-column>
                <el-table-column prop="planDays" label="">
                </el-table-column>
                <el-table-column prop="executionDays" label="">
                </el-table-column>
                <el-table-column prop="unforcedDays" label="" width="110">
                </el-table-column>
                <el-table-column prop="applyName" label="">
                </el-table-column>

                <el-table-column prop="createTime" label="">
                </el-table-column>
                <el-table-column label="" width="90">
                    <template slot-scope="scope">
                        <el-button type="primary" icon="el-icon-info" size="mini" round @click="showCommit(scope.row)"
                            v-if="scope.row.caseSubId"></el-button>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="success" @click="handleCheck(scope.row, 1)" size="mini" round></el-button>
                        <el-button type="primary" @click="handleCheck(scope.row, 2)" size="mini" round></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <!--  -->
        <el-dialog title="" :visible.sync="editDirectorRate" width="40%">
            <el-table :data="directorList" style="width: 100%">
                <el-table-column prop="name" label="">
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-input type="number" v-model="scope.row.value" placeholder="33%"></el-input>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-input v-model="scope.row.description" placeholder=""></el-input>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-input :value="scope.row.value*1.0/100*curObj.estimateValue" disabled></el-input>
                    </template>
                </el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editDirectorRate = false"> </el-button>
                <el-button type="primary" @click="submitDirectorRate()"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-card v-if="checkHistoryVisible">
            <el-page-header @back="checkHistoryVisible = false" content=""></el-page-header>
            <el-table :data="checkedApplyList" style="width: 100%">
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tag effect="dark" type="success" v-if="scope.row.status === 1"></el-tag>
                        <el-tag effect="dark" type="danger" v-else-if="scope.row.status === 2"></el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tag effect="dark" type="success" v-if="scope.row.caseSubId !== null"></el-tag>
                        <el-tag effect="dark" v-else-if="scope.row.type === 2"></el-tag>
                        <el-tag effect="dark" type="warning" v-else-if="scope.row.type === 1"></el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="description" label="">
                </el-table-column>
                
                <el-table-column prop="rejectReason" label="">
                </el-table-column>
                <el-table-column prop="applyName" label="">
                </el-table-column>
                <el-table-column prop="applyTime" label="">
                </el-table-column>
                <el-table-column prop="createTime" label="">
                </el-table-column>
                
                <el-table-column prop="checkName" label="">
                </el-table-column>
                <el-table-column prop="checkTime" label="">
                </el-table-column>
            </el-table>
            <el-pagination style="margin-top: 10px;" @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page="queryInfo.page" :page-sizes="[5, 8, 10, 15]" :page-size="queryInfo.pageSize"
                layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="commitVisible" width="40%">
            <el-form ref="commitFormRef" :model="commitForm" label-width="100px">
                <el-form-item label="">
                    <el-input v-model="commitForm.caseName" disabled></el-input>
                </el-form-item>
                <!--  -->
                <el-form-item label="">
                    <el-input v-model="commitForm.subName" disabled></el-input>
                </el-form-item>
                <!--  -->
                <el-form-item label="">
                    <el-card class="box-card">
                        <div v-for="o in commitForm.content" :key="o" class="text item">
                            {{ o }}
                        </div>
                        <label v-if="commitForm.content.length === 0"></label>
                    </el-card>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="commitVisible = false"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { getFinishApplyList, judgeFinishApply, endHistory } from '@/api/caseFinishApply'
import { mapActions, mapState } from 'vuex'
import { formatDate } from '@/utils/common'
import { countUser, submitDirectorValue } from '@/api/caseSubUser'
import { getById } from '@/api/caseSubCommit'
import { getCaseId } from '@/api/caseSub'
export default {
    data() {
        return {
            tableData: [],
            editDirectorRate: false,
            curObj: {},
            directorList: [],
            //
            checkHistoryVisible: false,
            queryInfo: {
                page: 1,
                pageSize: 5
            },
            total: 0,
            //
            checkedApplyList: [],
            commitVisible: false,
            commitForm: {
                caseName: '',
                subName: '',
                content: []
            }
        }
    },
    computed: {
        ...mapState(['user']),
        ...mapState('apply', ['finishList'])
    },
    created() {
        const pageSize = +localStorage.getItem("pim_check_finish_pageSize")
        this.queryInfo.pageSize = pageSize === 0 ? 5 : pageSize
        this.getFinish()
    },
    methods: {
        ...mapActions('apply', ['getFinish']),
        async handleCheck(row, status) {
            row.status = status
            row.checkUser = this.user.id
            this.curObj = { ...row }
            //
            if (status === 1) {
                //
                if (row.type !== null) {
                    this.$confirm(', ?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(async () => {
                        const res = await judgeFinishApply(this.curObj)
                        if (res.code === 200) {
                            this.$message.success(res.data)
                            setTimeout(() => this.getFinish(), 500)
                        } else {
                            this.$message.error(res.msg)
                        }
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: ''
                        });
                    });
                    return
                }
                //
                const res = await countUser(row.caseSubId)
                if (res.code !== 200) {
                    this.$message.error(res.msg)
                    return
                }
                this.directorList = res.data
                // 
                if (this.directorList.length === 1) {
                    this.directorList[0].value = 100
                    const res = await submitDirectorValue(this.directorList)
                    if(res.code!==200){
                        this.$message.error(res.msg)
                        return
                    }
                    // 
                    this.$message.success(res.data)
                    this.$confirm(', ?', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(async () => {
                        const res = await judgeFinishApply(this.curObj)
                        if (res.code === 200) {
                            this.$message.success(res.data)
                            setTimeout(() => this.getFinish(), 500)
                        } else {
                            this.$message.error(res.msg)
                        }
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: ''
                        });
                    });
                } else {
                    this.editDirectorRate = true
                }
            } else if (status === 2) {
                this.$prompt('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                }).then(async ({ value }) => {
                    if (value === '' || value === null) {
                        this.$message.error("")
                        return
                    }
                    row.status = status
                    row.checkUser = this.user.id
                    row.rejectReason = value
                    const res = await judgeFinishApply(row)
                    if (res.code === 200) {
                        this.$message.success(res.data)
                        setTimeout(() => this.getFinish(), 500)
                    } else {
                        this.$message.error(res.msg)
                    }
                }).catch(() => {
                    //
                    return
                })
            }
        },
        //
        async submitDirectorRate() {
            // 2023.10.27 100%
            // //
            // var sum = 0;
            // this.directorList.forEach(item => {
            //     sum += +item.value
            // })
            // // console.log(sum)
            // if (sum !== 100) {
            //     this.$message.error("100")
            //     return
            // }
            const res = await submitDirectorValue(this.directorList)
            if (res.code === 200) {
                this.$message.success(res.data)
                this.editDirectorRate = false
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(async () => {
                    const res = await judgeFinishApply(this.curObj)
                    if (res.code === 200) {
                        this.$message.success(res.data)
                        setTimeout(() => this.getFinish(), 500)
                    } else {
                        this.$message.error(res.msg)
                    }
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            } else {
                this.$message.error(res.msg)
                return
            }
        },
        openHistory() {
            this.checkHistoryVisible = true
            this.checkHistory()
        },
        async checkHistory() {
            const res = await endHistory(this.queryInfo)
            this.total = res.data.total
            this.checkedApplyList = res.data.records
        },
        handleSizeChange(value) {
            localStorage.setItem("pim_check_finish_pageSize", value)
            this.queryInfo.pageSize = value
            this.queryInfo.page = 1
            this.checkHistory()
        },
        handleCurrentChange(value) {
            this.queryInfo.page = value
            this.checkHistory()
        },
        openCaseSubDetail(row) {
        },
        async showCommit(row) {
            this.commitVisible = true
            var names = row.description.split("->")
            this.commitForm.caseName = names[0]
            this.commitForm.subName = names[1]
            // 
            var res = await getById(row.caseSubId)
            if (res.code == 200) {
                res = res.data
                //
                this.commitForm.content = []
                for (var i = 0; i < res.length; i++) {
                    this.commitForm.content.push(res[i].content)
                }
            } else
                this.$message.error(res.msg)
        },
        async handleDoubleClick(row, column) {
            if (row.caseSubId) {
                var res = await getCaseId(row.caseSubId)
                if (res.code == 200) {
                    res = res.data
                } else {
                    this.$message.error(res.msg)
                    return
                }
                this.$router.push({
                    name: '',
                    query: {
                        caseName: row.description.split("->")[0],
                        caseId: res
                    }
                })
            }
        },
        cellClassName({row,column,rowindex,columnIndex}){
            if(column.label===''||column.label===''){
                return 'heightLight'
            }
        }
    }
}
</script>

<style scoped>
.el-table >>> .heightLight{
    background: #60E5E5;
    animation:blink 0.6s linear 2;
}
@keyframes blink{
    0%{ background: #fff;}
    50%{
        background: #96E8E8;
    }
    100%{
        background: #60E5E5;
    }
}
</style>