<template>
  <div class="index" style="text-align: center;">
    <!--  -->
    <div>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>


    <el-card class="box-card">
      <div class="indexTable">
        <div class="query" style="margin-bottom: 15px;">
          <el-row :gutter="20">
            <!--  -->
            <el-col :span="4">
              <el-input class="queryText" placeholder="" v-model="queryText" clearable @change="handleQuery">
              </el-input>
            </el-col>

            <!--  -->
            <el-col :span="4" v-show="false" v-if="!showMode">
              <el-select v-model="queryStatus" placeholder="" clearable @change="handleQuery">
                <el-option v-for="item in levels" :key="item" :label="item" :value="item">
                </el-option>
              </el-select>
            </el-col>

            <!--  -->
            <el-col :span="4">
              <el-select v-model="queryDirector" placeholder="" clearable @change="handleQuery">
                <el-option v-for="item in users" :key="item.id" :label="item.name" :value="item.id">
                </el-option>
              </el-select>
            </el-col>

            <el-col :span="6" v-if="showMode">
              <el-date-picker v-model="start_stop_time" type="daterange" align="left" unlink-panels range-separator=""
                start-placeholder="" end-placeholder="" :picker-options="timeOptions" value-format="yyyy-MM-dd">
              </el-date-picker>
            </el-col>

            <el-col :span="2">
              <!--  -->
              <el-button type="primary" @click="handleQuery"> <i class="el-icon-search"></i></el-button>
            </el-col>

            <el-col :span="2">
              <!--  -->
              <el-button type="primary" @click="handleReset"> <i class="el-icon-s-tools"></i></el-button>
            </el-col>

            <el-col :span="3" :offset="showMode ? 3 : 9">
              <el-switch v-model="showMode" active-text="" inactive-text="">
              </el-switch>
            </el-col>

          </el-row>
        </div>
        <div ref="caseTableRef">
          <el-table :data="pageInfo" border stripe @cell-dblclick="handleDoubleClick" style="font-size:15px;" 
          :default-sort="{prop:'executionDays',order:'descending'}" @sort-change="handlesortChange">
            <el-table-column label="" width="120">
              <template slot-scope="scope">
                <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                  <el-button type="info" size="mini" icon="el-icon-info" round @click="showSub(scope.row)"></el-button>
                </el-tooltip>
                <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                  <el-button type="danger" size="mini" icon="el-icon-s-custom" round
                    @click="showPerson(scope.row)"></el-button>
                </el-tooltip>
              </template>

            </el-table-column>
            <el-table-column prop="name" label="" width="260" align="center">
            </el-table-column>
            <el-table-column prop="curStage" label="" align="center">
            </el-table-column>
            <el-table-column prop="level" label="" align="center">
            </el-table-column>
            <el-table-column prop="directorName" label="" align="center">
            </el-table-column>
            <el-table-column prop="startTime" label="" align="center">
            </el-table-column>
            <el-table-column prop="presetTime" label="" align="center">
            </el-table-column>
            <el-table-column prop="finishTime" label="" align="center">
            </el-table-column>
            <el-table-column prop="planDay" label="" sortable width="105" align="center">
            </el-table-column>
            <el-table-column prop="executionDays" label="" sortable width="105" align="center">
            </el-table-column>
            <el-table-column prop="unforcedDay" label="" sortable width="135" align="center">
            </el-table-column>
            <el-table-column prop="status" label="" align="center">
              <template slot-scope="scope">
                <el-tag effect="dark" type="info" v-if="scope.row.startTime === null" style="font-size:15px"
                  size="small"></el-tag>
                <el-tag effect="dark" type="warning" v-else-if="isInterrupt(scope.row)" style="font-size:15px"
                  size="small"></el-tag>
                <el-tag effect="dark" type="warning"
                  v-else-if="scope.row.finishTime !== null && scope.row.executionDays > scope.row.planDay"
                  style="font-size:15px" size="small"></el-tag>
                <el-tag effect="dark" type="primary"
                  v-else-if="scope.row.finishTime !== null && scope.row.executionDays <= scope.row.planDay"
                  style="font-size:15px" size="small"></el-tag>
                <el-tag effect="dark" type="danger" v-else-if="scope.row.executionDays > scope.row.planDay"
                  style="font-size:15px" size="small"></el-tag>
                <el-tag effect="dark" type="success" v-else style="font-size:15px" size="small"></el-tag>
              </template>

            </el-table-column>

          </el-table>
        </div>
        <!--  -->
        <el-pagination style="margin-top: 10px;text-align: left;" @size-change="handleSizeChange"
          @current-change="handleCurrentChange" :current-page.sync="page" :page-sizes="[5, 9, 10, 15, 20, 30]"
          :page-size="size" layout="total, sizes, prev, pager, next" :total="total">
        </el-pagination>
      </div>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="commitVisible" width="45%" @close="$refs.commitFormRef.resetFields()">
      <el-form ref="commitFormRef" :model="commitForm" :rules="rules" label-width="100px">
        <!--  -->
        <el-form-item label="">
          <el-input v-model="commitForm.caseName" disabled></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item label="">
          <el-input v-model="commitForm.subName" disabled></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item label="">
          <el-card class="box-card">
            <div v-for="(o, index) in commitForm.content" :key="o.id" class="text item">
              <span :style="{ color: index === 0 ? 'red' : 'black' }">{{ o.content }}</span>
              <el-button icon="el-icon-delete" size="mini" round type="danger" @click="deleteCommit(o.id)"></el-button>
            </div>
            <label v-if="commitForm.content.length === 0"></label>
          </el-card>
        </el-form-item>
        <!--  -->
        <el-form-item prop="newContent" label="" v-if="user.type === 1 || user.status === 2">
          <el-input type="textarea" v-model="commitForm.newContent"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitCommitForm" v-if="user.type === 1 || user.status === 2"></el-button>
        <el-button @click="commitVisible = false"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import { mapActions, mapState, mapMutations } from 'vuex'
import { deleteCommit, getById, saveCommit } from '@/api/caseSubCommit';
import { getExecuting } from '@/api/caseSub';
import { getUserListWithAssistants } from '@/api/user'
import html2canvas from 'html2canvas'
import { timeAdd } from '@/utils/common';
export default {
  name: 'indexPage',
  data() {
    return {
      pageInfo: [],
      page: 1,
      size: 9,
      total: 0,
      queryText: '',
      caseInfo: [],
      levels: ['', ''],
      queryStatus: '',
      queryDirector: null,
      commitVisible: false,
      commitForm: {
        caseName: '',
        subName: '',
        content: [],
        newContent: '',
        caseSubId: null,
      },
      //
      rules: {
        newContent: [
          {
            required: true, message: '', trigger: 'blur'
          },
          {
            min: 8, max: 255, message: '8255', trigger: 'blur'
          }
        ]
      },
      //(falsetrue)
      showMode: false,
      start_stop_time: '',
      //
      timeOptions: {
        shortcuts: [{
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 180);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 365);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      //
      users: [],
      executingList: [],
    }
  },
  async created() {
    await this.getCaseList(this.showMode)
    this.caseInfo = this.caseList
    this.getTableDate()
    this.getAllUser()
    this.getExecutingList()
  },
  mounted() {
    var pageSize = +JSON.parse(localStorage.getItem('pim_caseTable_pageSize'))
    this.size = pageSize === 0 ? 10 : pageSize
  },
  watch: {
    queryList: {
      handler() {
        this.caseInfo = this.queryList
      }
    },
    caseList: {
      handler() {
        this.caseInfo = this.caseList
      }
    },
    caseInfo: {
      handler() {
        this.getTableDate()
      }
    },
    showMode: {
      handler() {
        this.getCaseList(this.showMode)
        this.getTableDate()
      }
    }
  },
  computed: {
    ...mapState('caseM', ['caseList', 'queryList']),
    ...mapState(['user']),
    ...mapState('apply', ['subList'])
  },
  //
  beforeRouteEnter(to, from, next) {
  
    next((vm) => {
      // 
      if ('caseName' in to.query) {
        vm.queryText = to.query.caseName
        vm.handleQuery()
      }
    })
  },
  methods: {
    ...mapActions('caseM', ['getCaseList']),
    ...mapMutations('caseM', ['queryCase']),
    async getExecutingList() {
      const res = await getExecuting()
      if (res.code === 200) {
        this.executingList = res.data
        this.executingList.forEach(item => item.presetTime = timeAdd(item.startTime, item.planDays, +item.unforcedDays, +item.applyDelay))
      }
    },
    async getAllUser() {
      const res = await getUserListWithAssistants()
      if (res.code === 200) {
        this.users = res.data
      }
    },
    showSub(row) {
      this.$router.push({
        name: '',
        query: {
          caseId: row.id,
          caseName: row.name
        }
      })
    },
    //
    showPerson(row) {
      this.$router.push({
        name: '',
        query: {
          caseId: row.id,
          caseName: row.name,
          planDay: row.planDay,
          executionDays: row.executionDays
        }
      })
    },
    transformStatus(status) {
      if (status === "")
        return 0
      else if (status === "")
        return 1
      else if (status === "")
        return 2
      else if (status === "")
        return 3
      else if (status === "")
        return 4
      else if (status === "")
        return 5
    },
    //
    handleQuery() {
      //1. 
      //2. 
      //4. 
      //5. 
      var queryObj = {}
      if (this.queryText !== '')
        queryObj.name = this.queryText
      // 
      if (!this.showMode && this.queryStatus !== '' && this.queryStatus !== null) {
        // this.queryStatus = this.transformStatus(this.queryStatus)
        queryObj.status = this.transformStatus(this.queryStatus)
      }

      //
      if (this.showMode && this.start_stop_time !== null && this.start_stop_time !== '') {
        queryObj.startTime = this.start_stop_time[0]
        queryObj.endTime = this.start_stop_time[1]
      }

      //
      if (this.queryDirector !== null && this.queryDirector !== '')
        queryObj.director = this.queryDirector
      this.queryCase(queryObj)

      this.page = 1
      // this.caseInfo = this.queryList
    },
    handleReset() {
      //queryList
      this.queryCase({})
      //
      this.queryText = ''
      this.start_stop_time = ''
    },
    //
    handleSizeChange(val) {
      localStorage.setItem('pim_caseTable_pageSize', JSON.stringify(val))
      this.size = val
      this.page = 1
      this.getTableDate()
    },
    //
    handleCurrentChange(val) {
      this.page = val
      this.getTableDate()
    },
    getTableDate() {
      this.pageInfo = this.caseInfo.slice((this.page - 1) * this.size,
        this.page * this.size)
      this.total = this.caseInfo.length
    },
    //
    async handleDoubleClick(row, column) {
      if (column.label === '' && !this.showMode) {
        this.commitForm.caseName = row.name
        this.commitForm.subName = row.curStage
        this.commitForm.caseSubId = row.curStageId
        this.getAndShowCommit()
      }
    },
    //
    async getAndShowCommit() {
      // 
      var res = await getById(this.commitForm.caseSubId)
      res = res.data
      //
      this.commitForm.content = []
      for (var i = 0; i < res.length; i++) {
        this.commitForm.content.push({ content: res[i].content, id: res[i].id })
      }
      this.commitVisible = true
    },
    async submitCommitForm() {
      //
      this.$refs.commitFormRef.validate(async valid => {
        if (!valid) return;
        const commmitObj = {}
        commmitObj.caseSubId = this.commitForm.caseSubId
        commmitObj.content = this.commitForm.newContent
        commmitObj.createUser = this.user.id
        var res = await saveCommit(commmitObj)
        this.$message(res.data)
        //
        this.commitVisible = false
      })

    },
    //
    deleteCommit(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const res = await deleteCommit(id)
        if (res.code === 200) {
          this.$message.success(res.data)
          this.getAndShowCommit()
        } else
          this.$message.error(res.msg)
      })
    },
    isInterrupt(row) {
      for (var i = 0; i < this.subList.length; i++) {
        if (row.name === this.subList[i].caseName)
          return true
      }
      return false
    },
    // 
    async captureScreenshot() {
      // DOM
      const screenshotArea = this.$refs.caseTableRef;
      // html2canvas
      await html2canvas(screenshotArea).then(canvas => {
        // canvasURL
        const imgData = canvas.toDataURL();

        // URL
        const img = new Image();
        img.src = imgData;

        // 
        const newWindow = window.open();
        newWindow.document.write('<img src="' + img.src + '" />');
      });

    },
    handlesortChange(column) {
      if (column.order === 'ascending')
        column.order = 1
      else if (column.order === 'descending')
        column.order = -1
      else {
        column.order = -1
        column.prop = 'executionDays'
      }
      this.caseInfo.sort((a, b) => {
        if (a[column.prop] < b[column.prop])
          return -1 * column.order
        else if (a[column.prop] > b[column.prop])
          return 1 * column.order
        else return 0
      })
    },
  }
}
</script>

<style lang="less" scoped>
.cell-content {
  height: 20px;
  line-height: 20px;
  padding: 0;
}

</style>

