<template>
    <div>
        <div>
            <el-breadcrumb separator="/">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <el-card>
            <el-row>
                <el-col :span="2">
                    <el-button type="primary" icon="el-icon-circle-plus" @click="openAddTask()"></el-button>
                </el-col>
                <el-col :span="4" :offset="18">
                    <el-radio-group v-model="showMode" style="margin-left:50px;">
                        <el-radio-button label=""><i class="el-icon-picture"></i></el-radio-button>
                        <el-radio-button label=""><i class="el-icon-s-custom"></i></el-radio-button>
                    </el-radio-group>
                </el-col>
            </el-row>
            <br>
            <!--  -->
            <div class="overview" v-if="showMode === ''">

                <el-card class="executing-container">
                    <h4><i class='el-icon-s-flag'></i></h4>
                    <el-table :data="executingList" style="font-size: 15px;">
                        <el-table-column type="index"></el-table-column>
                        <el-table-column prop="description" label="" align="center" width="320"></el-table-column>
                        <el-table-column prop="userNames" label="" width="220" align="center"></el-table-column>
                        <el-table-column prop="startTime" label="" align="center" width="100"></el-table-column>
                        <el-table-column>
                            <template slot-scope="scope">
                                <el-button type="primary" icon="el-icon-position" round size="mini"
                                    @click="redirectToDetailPage(scope.row)"></el-button>
                            </template>
                        </el-table-column>

                    </el-table>
                </el-card>
                <div class="exception-container">
                    <el-card class="delay-container">
                        <h4><i class='el-icon-error'></i></h4>
                        <el-table :data="delayList">
                            <el-table-column type="index" width="20"></el-table-column>
                            <el-table-column prop="description" label="" align="center" width="320"></el-table-column>
                            <el-table-column prop="userNames" label=""></el-table-column>
                            <el-table-column prop="delayDays" label="()"></el-table-column>
                            <el-table-column>
                                <template slot-scope="scope">
                                    <el-button type="danger" icon="el-icon-position" round size="mini"
                                        @click="redirectToDetailPage(scope.row)"></el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-card>
                    <el-card class="pause-container">
                        <h4><i class='el-icon-video-pause'></i></h4>
                        <el-table :data="pausingList">
                            <el-table-column type="index" width="20"></el-table-column>
                            <el-table-column prop="description" label="" align="center" width="320"></el-table-column>
                            <el-table-column prop="userNames" label="" width="150"></el-table-column>
                            <el-table-column prop="pauseStart" label="" width="100"></el-table-column>
                            <el-table-column label="">
                                <template slot-scope="scope">
                                    {{ `${scope.row.pauseDays} ` }}
                                </template>
                            </el-table-column>
                            <el-table-column>
                                <template slot-scope="scope">
                                    <el-tooltip class="item" effect="dark" :content="scope.row.pauseDesc" placement="top">
                                        <el-button type="info" icon="el-icon-position" round size="mini"
                                            @click="redirectToDetailPage(scope.row)"></el-button>
                                    </el-tooltip>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-card>
                </div>
            </div>
            <!--  -->
            <div class="person" v-if="showMode === ''">
                <!--  -->
                <el-row>



                    <el-col :span="3" :offset="21">
                        <el-select v-model="curUser" placeholder="" @change="handleUserChange()">
                            <el-option-group v-for="group in directorOptions" :key="group.value" :label="group.label">
                                <el-option v-for="item in group.children" :key="item.value" :label="item.label"
                                    :value="item.value">
                                </el-option>
                            </el-option-group>
                        </el-select>
                    </el-col>
                </el-row>
                <br>
                <el-table :data="taskList">
                    <el-table-column label="">
                        <template slot-scope="scope">
                            <el-tag effect="dark" type="warning" v-if="scope.row.type === 1"></el-tag>
                            <el-tag effect="dark" type="primary" v-else-if="scope.row.type === 2"></el-tag>
                        </template>
                    </el-table-column>
                    <el-table-column prop="description" label=""></el-table-column>
                    <el-table-column prop="startTime" label=""></el-table-column>
                    <el-table-column prop="predictTime" label=""></el-table-column>
                    <el-table-column prop="finishTime" label=""></el-table-column>
                    <el-table-column prop="planDays" label=""></el-table-column>
                    <el-table-column prop="executionDays" label=""></el-table-column>
                    <el-table-column prop="unforcedDays" label=""></el-table-column>
                    <el-table-column prop="applyDelay" label=""></el-table-column>
                    <el-table-column label="">
                        <template slot-scope="scope">
                            <el-tooltip class="item" effect="dark" content="" placement="right-start">
                                <el-button icon="el-icon-edit" type="primary" size="mini" round
                                    @click="openTaskDetail(scope.row)">
                                </el-button>
                            </el-tooltip>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="taskDetailVisible" width="30%">
            <el-form ref="editCaseSubRef" label-width="90px">
                <el-form-item label="">
                    <el-input type="textarea" v-model="curTaskObj.description"></el-input>
                </el-form-item>


                <el-row>
                    <el-col :span="10">
                        <el-form-item label="">
                            <el-input v-model="curTaskObj.planDays"></el-input>
                        </el-form-item>
                    </el-col>

                    <el-col :span="12" :offset="2">
                        <el-form-item label="">
                            <el-select v-model="curTaskObj.director" placeholder="" @change="handleUserChange()">
                                <el-option-group v-for="group in directorOptions" :key="group.value" :label="group.label">
                                    <el-option v-for="item in group.children" :key="item.value" :label="item.label"
                                        :value="item.value">
                                    </el-option>
                                </el-option-group>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>

                <!--  -->
                <el-form-item label="">
                    <el-date-picker v-model="curTaskObj.startTime" type="date" placeholder="">
                    </el-date-picker>
                </el-form-item>

                <!--  -->

                <el-form-item label="">
                    <el-date-picker v-model="curTaskObj.finishTime" type="date" placeholder="">
                    </el-date-picker>
                </el-form-item>

                <el-row>
                    <!--  -->
                    <el-col :span="12">
                        <el-form-item label="">
                            <el-input v-model="curTaskObj.unforcedDays"></el-input>
                        </el-form-item>
                    </el-col>
                    <!--  -->
                    <el-col :span="12">
                        <el-form-item label="">
                            <el-input v-model="curTaskObj.applyDelay"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="taskDetailVisible = false"> </el-button>
                <el-button type="primary" @click="submitTaskChange()"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="addTaskVisible" width="30%">
            <el-form ref="addTaskFormRef" :rules="applyTaskRules" :model="addTaskObj" label-width="80px">
                <el-form-item label="" prop="type">
                    <el-select v-model="addTaskObj.type" placeholder="">
                        <el-option v-for="item in [{ label: '', value: 2 }, { label: '', value: 1 }]"
                            :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="description">
                    <el-input type="textarea" placeholder="" v-model="addTaskObj.description"></el-input>
                </el-form-item>
                <el-form-item label="" prop="planDays">
                    <el-input type="number" placeholder="" v-model.number="addTaskObj.planDays"></el-input>
                </el-form-item>
                <el-form-item label="" prop="director">
                    <el-select v-model="addTaskObj.director" placeholder="">
                        <el-option-group v-for="group in directorOptions" :key="group.value" :label="group.label">
                            <el-option v-for="item in group.children" :key="item.value" :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-option-group>
                    </el-select>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addTaskVisible = false"> </el-button>
                <el-button type="primary" @click="addTask()"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { allTaskList, updateTask, addTask, getExecutingTask } from '@/api/task';
import { getUserListWithAssistants } from '@/api/user';
import { format4back, formatDate, timeAdd, timeSub } from '@/utils/common';
import { mapState } from 'vuex';

export default {
    data() {
        var checkPlandays = (rule, value, callback) => {
            if (value === null || value === '')
                callback(new Error(""))
            if (value < 1)
                callback(new Error("1"))
            callback()
        }
        return {
            taskList: [],
            //
            directorOptions: [
                {
                    value: 0,
                    label: '',
                    children: []
                },
                {
                    value: 1,
                    label: '',
                    children: []
                }, {
                    value: 2,
                    label: 'IE',
                    children: []
                }
            ],
            curUser: null,
            taskDetailVisible: false,
            curTaskObj: {},
            //
            addTaskVisible: false,
            applyTaskRules: {
                type: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                description: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                planDays: [
                    { required: true, message: '', trigger: 'blur' },
                    { validator: checkPlandays, trigger: 'blur' }
                ],
                director: [
                    { required: true, message: '', trigger: 'change' }
                ],
            },
            addTaskObj: {
                director: null,
            },
            showMode: '',
            executingList: [],
            delayList: [],
            pausingList: [],
        }
    },
    async created() {
        await this.getAllUser()
        this.curUser = this.directorOptions[0].children[0].value
        this.getAllTaskList()
        this.getExecutingTask()
    },
    computed: {
        ...mapState(['user'])
    },
    methods: {
        async getAllTaskList() {
            const res = await allTaskList(this.curUser)
            if (res.code === 200) {
                this.taskList = res.data
                this.taskList.forEach(item => {
                    item.predictTime = timeAdd(item.startTime, item.planDays)
                    if (item.finishTime !== null) {
                        item.executionDays = timeSub(item.startTime, item.finishTime)
                        item.executionDays -= item.unforcedDays
                    } else {
                        item.executionDays = timeSub(item.startTime, new Date())
                        item.executionDays -= item.unforcedDays
                    }
                })
            } else {
                this.$message.error(res.msg)
            }
        },
        //
        async getAllUser() {
            //
            var { data: res } = await getUserListWithAssistants()
            for (var i = 0; i < res.length; i++) {
                this.directorOptions[res[i].status].children.push({ value: res[i].id, label: res[i].name })
            }
        },
        handleUserChange() {
            this.getAllTaskList()
        },
        openTaskDetail(row) {
            this.taskDetailVisible = true
            //s
            this.curTaskObj = {}
            this.curTaskObj = { ...row }
        },
        //
        async submitTaskChange() {
            //
            if (this.curTaskObj.description === '' || this.curTaskObj.description === null) {
                this.$message.error("")
                return
            }
            if (this.curTaskObj.planDays === '' || this.curTaskObj.planDays === null) {
                this.$message.error("")
                return
            }
            if (this.curTaskObj.startTime === null && this.curTaskObj.finishTime !== null) {
                this.$message.error("")
                return
            }
            //
            this.curTaskObj.startTime = format4back(this.curTaskObj.startTime)
            this.curTaskObj.finishTime = format4back(this.curTaskObj.finishTime)
            const res = await updateTask(this.curTaskObj)
            if (res.code === 200) {
                this.$message.success(res.data)
                this.taskDetailVisible = false
                this.getAllTaskList()
            } else {
                this.$message.error(res.msg)
            }
        },
        //
        openAddTask() {
            this.addTaskVisible = true
        },
        addTask() {
            this.$refs.addTaskFormRef.validate(async (valid) => {
                if (valid) {
                    this.addTaskObj.createUser = this.user.id
                    const res = await addTask(this.addTaskObj)
                    if (res.code === 200) {
                        this.addTaskVisible = false
                        this.$message.success(res.data)
                        this.getAllTaskList()
                        this.getExecutingTask()
                    } else {
                        this.$message.error(res.msg)
                    }
                }
            })

        },
        // 
        async getExecutingTask() {
            const res = await getExecutingTask()
            this.executingList = res.data
            this.executingList.forEach(item => {
                if (item.pausing) {
                    item.pauseDays = timeSub(item.pauseStart, new Date())
                    this.pausingList.push(item)
                }
                item.delayDays = timeSub(item.startTime, new Date()) - item.planDays - +item.unforcedDays
                if (item.delayDays > 0) {
                    item.pauseStart = formatDate(item.pauseStart)
                    this.delayList.push(item)
                    item.is_delay = true
                } else
                    item.is_delay = false
            })
            this.executingList = this.executingList.filter(item => !item.pausing)
            this.executingList = this.executingList.filter(item => !item.is_delay)
        },
        // 
        redirectToDetailPage(row) {
            this.$router.push({
                name: '',
                query: {
                    userName: row.userNames
                }
            })
        }
    }
}
</script>

<style scoped>
.overview {
    display: flex;
    flex-direction: row;
}

.executing-container {
    width: 50%;
    margin-right: 1%;
}

.exception-container {
    width: 49%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.delay-container {
    margin-bottom: 10px;
}
</style>