<template>
    <div><!--  -->
        <div>
            <el-breadcrumb separator="/">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <el-card>
            <!--  -->
            <el-row :gutter="20">
                <el-col :span="2" :offset="20">
                    <el-button type="success" icon="el-icon-plus" @click="initApplyCaseSub()"></el-button>
                </el-col>

                <el-col :span="2">
                    <el-button type="primary" icon="el-icon-plus" @click="applyTaskVisible = true"></el-button>
                </el-col>
            </el-row>
            <h2></h2>
            <!--  -->
            <el-table :data="userInfo" @cell-dblclick="handleDoubleClick">
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-progress v-if="!scope.row.pausing" :stroke-width="24" :percentage="scope.row.percentage"
                            :status="scope.row.finishedOwnWork ? 'primary' : 'leftDelay' in scope.row ? scope.row.leftDelay >= 0 ? 'warning' : 'exception' : 'success'">
                        </el-progress>
                        <el-progress v-else :stroke-width="24" :percentage="100" color="#909399" :show-text="false">
                        </el-progress>
                    </template>
                </el-table-column>
                <el-table-column prop="comment" label=""></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tag effect="dark" type="success" v-if="scope.row.type === 0"></el-tag>
                        <el-tag effect="dark" v-else-if="scope.row.type === 2"></el-tag>
                        <el-tag effect="dark" type="warning" v-else-if="scope.row.type === 1"></el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="description" label=""></el-table-column>
                <el-table-column prop="startTime" label=""></el-table-column>
                <el-table-column prop="planDays" label=""></el-table-column>
                <el-table-column prop="pauseStart" label=""></el-table-column>
                <el-table-column prop="executionDays" label=""></el-table-column>
                <el-table-column prop="unforcedDays" label=""></el-table-column>
                <el-table-column prop="applyDelay" label=""></el-table-column>
                <el-table-column label="" width="180">
                    <template slot-scope="scope">
                        <el-tooltip class="item" effect="dark" content="" placement="top"
                            v-if="!scope.row.pausing && !scope.row.delayChecking">
                            <el-button size="mini" type="warning" icon="el-icon-timer" round
                                @click="openDelayApply(scope.row)"></el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" content="" placement="top"
                            v-if="!scope.row.pausing && scope.row.delayChecking">
                            <el-button size="mini" type="primary" icon="el-icon-timer" round></el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" content="" placement="top"
                            v-if="!scope.row.pausing && !scope.row.finishChecking">
                            <el-button size="mini" type="success" icon="el-icon-success" round
                                @click="tryFinish(scope.row)"></el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" content="" placement="top"
                            v-if="!scope.row.pausing && scope.row.finishChecking">
                            <el-button size="mini" type="primary" icon="el-icon-success" round></el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" content="" placement="top">
                            <el-button icon="el-icon-video-pause" type="info" size="mini" round
                                @click="openPause(scope.row)" v-if="!scope.row.pausing">
                            </el-button>
                        </el-tooltip>
                        <el-tooltip class="item" effect="dark" content="" placement="top">
                            <el-button icon="el-icon-video-play" type="success" size="mini" round
                                @click="finishPause(scope.row.pauseId)" v-if="scope.row.pausing">
                            </el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>

        <!--  -->
        <el-card style="margin-top: 5px;" v-if="exceptionList.length > 0">
            <h2></h2>
            <!--  -->
            <el-table :data="exceptionList" stripe border style="width: 100%">
                <el-table-column prop="caseName" label=""></el-table-column>
                <el-table-column prop="subName" label=""></el-table-column>
                <el-table-column prop="level" label=""></el-table-column>
                <el-table-column prop="planDays" label=""></el-table-column>
                <el-table-column prop="lastFinishTime" label=""></el-table-column>
                <el-table-column prop="interruptDays" label="()"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tooltip class="item" effect="dark" content="" placement="top">
                            <el-button size="mini" type="success" icon="el-icon-success" round
                                @click="tryStart(scope.row)"></el-button>
                        </el-tooltip>

                        <el-tooltip class="item" effect="dark" content="" placement="top">
                            <el-button size="mini" type="primary" icon="el-icon-s-promotion" round
                                @click="openCaseDetail(scope.row)"></el-button>
                        </el-tooltip>

                    </template>
                </el-table-column>

            </el-table>
        </el-card>

        <br>
        <el-card class="gantt-chart">
            <div id="ganttChart" style="width: 100%; height: 500px;"></div>
        </el-card>

        <!-- <div class="charts-area">
            <el-card class="pie-chart">
                <h2></h2><span>()</span>
                <div id="taskType" style="width: 100%; height: 300px;"></div>
            </el-card>
            <el-card class="bar-chart">
                <h2></h2><span>()</span>
                <div id="taskAchieve" style="width: 100%; height: 300px"></div>
            </el-card>
        </div> -->


        <el-dialog title="" :visible.sync="applyDelayVisible" width="30%" @close="closeApplyDelay">
            <el-form ref="applyDelayFormRef" :rules="delayRules" :model="delayApplyObject" label-width="110px" class="form">
                <el-form-item label="">
                    <el-input :value="delayApplyObject.type === 0 ? '' : delayApplyObject.type === 1 ? '' : ''"
                        disabled></el-input>
                </el-form-item>

                <el-form-item label="">
                    <el-input v-model="delayApplyObject.description" disabled></el-input>
                </el-form-item>

                <el-form-item label="" prop="delayType">
                    <el-select v-model="delayApplyObject.delayType" placeholder="">
                        <el-option v-for="item in ['', '']" :key="item" :label="item" :value="item">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label=":" prop="applyReason">
                    <el-input autosize type="textarea" v-model="delayApplyObject.applyReason"
                        placeholder=""></el-input>
                </el-form-item>

                <el-form-item label="" prop="applyDays">
                    <el-input type="number" v-model.number="delayApplyObject.applyDays" placeholder=""
                        @input="computePresetTime"></el-input>
                </el-form-item>

                <el-form-item label="" v-if="delayApplyObject.presetFinishTime">
                    <el-input :value="delayApplyObject.presetFinishTime" disabled></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="applyDelayVisible = false"> </el-button>
                <el-button type="primary" @click="submitDelayApply()"> </el-button>
            </span>
        </el-dialog>


        <el-dialog title="" width="30%" :visible.sync="updateDescriptionVisible" @close="closeDescriptionForm">
            <h2></h2>
            <br>
            <el-form label-width="90px" ref="descriptionFormRef" class="form">
                <el-form-item v-for="item in directors" :label="item.name" :key="item.id">
                    <el-input v-model="item.description" placeholder="CCD15"
                        :disabled="item.userId !== user.id"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="updateDescriptionVisible = false"> </el-button>
                <el-button type="primary" @click="submitDirectorJobDescription()"> </el-button>
            </span>
        </el-dialog>

        <el-dialog title="" :visible.sync="applyCaseSubVisible" width="40%"
            @close="$refs.applyCaseSubFormRef.resetFields()">
            <el-form ref="applyCaseSubFormRef" :rules="applyCaseSubRules" :model="applyCaseSubUser" label-width="100px">
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="" prop="caseId">
                            <el-select v-model="applyCaseSubUser.caseId" placeholder="" @change="getUnfinishedSubList()">
                                <el-option v-for="item in unFinishedCaseList" :key="item.id" :label="item.name"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>

                    <el-col :span="12">
                        <el-form-item label="" prop="caseSubId">
                            <el-select v-model="applyCaseSubUser.caseSubId" placeholder="" @change="flushCaseSubId">
                                <el-option v-for="item in unfinishedSubList" :key="item.id" :label="item.subName"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-form-item label="" prop="description">
                    <el-input v-model="applyCaseSubUser.description" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="applyCaseSubVisible = false"> </el-button>
                <el-button type="primary" @click="submitApplyCaseSub()"> </el-button>
            </span>
        </el-dialog>

        <el-dialog title="" :visible.sync="applyTaskVisible" width="30%" @close="$refs.applyTaskFormRef.resetFields()">
            <el-form ref="applyTaskFormRef" :rules="applyTaskRules" :model="applyTask" label-width="80px">
                <el-form-item label="" prop="type">
                    <el-select v-model="applyTask.type" placeholder="">
                        <el-option v-for="item in [{ label: '', value: 2 }, { label: '', value: 1 }]"
                            :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="description">
                    <el-input type="textarea" placeholder="" v-model="applyTask.description"></el-input>
                </el-form-item>
                <el-form-item label="" prop="planDays">
                    <el-input type="number" placeholder="" v-model.number="applyTask.planDays"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="applyTaskVisible = false"> </el-button>
                <el-button type="primary" @click="submitApplyTask()"> </el-button>
            </span>
        </el-dialog>

        <el-dialog title="" :visible.sync="selectFinishTimeVisible" width="30%">
            <el-date-picker v-model="curTask.applyTime" type="date" placeholder="" value-format="yyyy-MM-dd HH:mm:ss"
                @input="changeTime">
            </el-date-picker>
            <span slot="footer" class="dialog-footer">
                <el-button @click="selectFinishTimeVisible = false"> </el-button>
                <el-button type="primary" @click="submitFinish()"> </el-button>
            </span>
        </el-dialog>

        <el-dialog title="" :visible.sync="selectStartTimeVisible" width="30%">
            <el-date-picker v-model="curInterruptTask.startTime" type="date" placeholder=""
                value-format="yyyy-MM-dd HH:mm:ss" @input="changeTime">
            </el-date-picker>
            <span slot="footer" class="dialog-footer">
                <el-button @click="selectStartTimeVisible = false"> </el-button>
                <el-button type="primary" @click="launch()"> </el-button>
            </span>
        </el-dialog>

        <el-drawer direction="ltr" :visible.sync="delayDrawer" :with-header="false" size="50%">
            <el-table :data="delayList">
                <el-table-column prop="applyReason" label="" width="550">
                </el-table-column>
                <el-table-column prop="applyDays" label="">
                </el-table-column>
                <el-table-column prop="delayType" label="">
                </el-table-column>
            </el-table>
        </el-drawer>

        <el-dialog title="" :visible.sync="pauseVisible" width="30%">
            <el-input v-model="pauseObj.description" placeholder=""></el-input>
            <span slot="footer" class="dialog-footer">
                <el-button @click="pauseVisible = false"> </el-button>
                <el-button type="primary" @click="startPause()"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { mapState } from 'vuex'
import { timeSub, timeAdd, formatDate, format4back } from '@/utils/common'
import { unFinishedCaseList } from '@/api/case'
import { unfinishedSubList, startOrFinish } from '@/api/caseSub'
import { taskList, recentTaskList, recentHalfYear, getExceptionList } from '@/api/task'
import { saveApply, getDelayById } from '@/api/caseDelayApply'
import { getFinishListByUserId, saveFinishApply } from '@/api/caseFinishApply'
import { countUser, updateDescription } from '@/api/caseSubUser'
import { saveApplyCaseSub } from '@/api/applyCaseSub'
import { saveApplyTask } from '@/api/applyTask'
import { startPause, finishPause } from '@/api/pause'

export default {
    name: 'case4me',
    data() {
        var checkDays = (rule, value, callback) => {
            if (value <= 0 || value > 90)
                callback(new Error("1-90"))
            callback()
        }
        var checkPlandays = (rule, value, callback) => {
            if (value === null || value === '')
                callback(new Error(""))
            if (value < 1)
                callback(new Error("1"))
            callback()
        }
        return {
            userInfo: [],
            applyDelayVisible: false,
            delayApplyObject: {},
            delayRules: {
                applyReason: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                applyDays: [
                    { required: true, message: '', trigger: 'blur' },
                    { validator: checkDays, trigger: 'blur' }
                ],
                delayType: [
                    { required: true, message: '', trigger: 'change' }
                ]
            },
            applyCaseSubRules: {
                caseId: [
                    { required: true, message: '', trigger: 'change' }
                ],
                caseSubId: [
                    { required: true, message: "", trigger: 'blur' }
                ],
                description: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            },
            applyTaskRules: {
                type: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                description: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                planDays: [
                    { required: true, message: '', trigger: 'blur' },
                    { validator: checkPlandays, trigger: 'blur' }
                ]
            },
            //
            updateDescriptionVisible: false,
            directors: [],
            // 
            applyCaseSubVisible: false,
            //
            applyCaseSubUser: {},
            //
            unFinishedCaseList: [],
            //
            unfinishedSubList: [],
            //
            applyTaskVisible: false,
            applyTask: {},
            //
            curTask: {},
            //
            selectFinishTimeVisible: false,
            //
            delayApplyList: [],
            //
            finishApplyList: [],
            //
            exceptionList: [],
            //
            selectStartTimeVisible: false,
            //
            curInterruptTask: {},
            // 
            gantt: null,
            // 
            delayList: [],
            // 
            delayDrawer: false,
            // 
            pauseVisible: false,
            // 
            pauseObj: {
                description: '',
                taskId: null,
                caseSubId: null,
                target: ''
            }
        }
    },
    async created() {
        // 
        // this.getDelayApplyList()
        // this.getFinishApplyList()
        await this.getTaskByUserId()
        this.initGantt()
        this.getUnfinishedCaseList()
        this.getExceptionList()
    },
    mounted() {
        // this.initPie()
        // this.initBur()

        this.applyCaseSubVisible = this.$route.query.applyCaseSubVisible
        this.applyTaskVisible = this.$route.query.applyTaskVisible
    },
    computed: {
        ...mapState(['user'])
    },
    methods: {
        // 
        async getExceptionList() {
            const res = await getExceptionList(this.user.id)
            if (res.code === 200) {
                this.exceptionList = res.data
                this.exceptionList.forEach(item => {
                    item.lastFinishTime = formatDate(item.lastFinishTime)
                    item.interruptDays = timeSub(item.lastFinishTime, new Date()) - 1
                })
            } else {
                this.$message.error(res.msg)
            }
        },
        async getDelayApplyList() {
            const res = await getDelayListByUserId(this.user.id)
            if (res.code === 200) {
                this.delayApplyList = res.data
            } else {
                this.$message.error(res.msg)
            }
        },
        async getFinishApplyList() {
            const res = await getFinishListByUserId(this.user.id)
            if (res.code === 200) {
                this.finishApplyList = res.data
            } else {
                this.$message.error(res.msg)
            }
        },
        async getTaskByUserId() {
            const res = await taskList(this.user.id)
            this.userInfo = res.data

            for (var i = 0; i < this.userInfo.length; i++) {
                this.userInfo[i].executionDays = timeSub(this.userInfo[i].startTime, new Date())
                var presetTime = new Date(this.userInfo[i].startTime)
                presetTime = presetTime.setDate(presetTime.getDate() + this.userInfo[i].planDays + this.userInfo[i].unforcedDays - 1)
                this.userInfo[i].presetTime = presetTime
                //
                var today = new Date()
                var delayCount = 0
                today.setHours(0, 0, 0, 0)
                // 
                this.userInfo[i].pauseStart = this.userInfo[i].pauseStart ? formatDate(this.userInfo[i].pauseStart) : null
                //
                if (today <= this.userInfo[i].presetTime) {
                    //
                    var costDay = timeSub(this.userInfo[i].startTime, today)
                    // /
                    this.userInfo[i].percentage = costDay * 1.0 / (this.userInfo[i].planDays + +this.userInfo[i].unforcedDays) * 100
                } else {
                    //(-1)
                    var delayDay = timeSub(this.userInfo[i].presetTime, today) - 1
                    //
                    this.userInfo[i].leftDelay = this.userInfo[i].applyDelay - delayDay
                    //
                    if (this.userInfo[i].leftDelay >= 0) {
                        this.userInfo[i].percentage = (delayDay / this.userInfo[i].applyDelay) * 100
                    } else {
                        this.userInfo[i].percentage = (timeSub(this.userInfo[i].startTime, today) / this.userInfo[i].planDays) * 100
                        delayCount++
                    }
                }
                this.userInfo[i].presetTime = formatDate(this.userInfo[i].presetTime)
                this.userInfo[i].comment = this.percentageText(this.userInfo[i])
                if (this.userInfo[i].percentage > 100)
                    this.userInfo[i].percentage = 100

            }
        },

        percentageText(row) {
            if (row.pausing)
                return ""
            if ('leftDelay' in row) {
                if (row.leftDelay >= 0) {
                    return `${row.leftDelay}`
                }
                return `${row.applyDelay - row.leftDelay}`
            }
            //+1
            return `${row.planDays + +row.unforcedDays + 1 - row.executionDays}`
        },
        //dialog
        openDelayApply(row) {
            this.applyDelayVisible = true
            this.delayApplyObject = { ...row }
            // 
            this.delayApplyObject.presetFinishTime = null
        },
        //dialog
        closeApplyDelay() {
            this.$refs.applyDelayFormRef.resetFields()
        },
        closeDescriptionForm() {
            this.$refs.descriptionFormRef.resetFields()
        },
        submitDelayApply() {
            this.$refs.applyDelayFormRef.validate(async (valid) => {
                if (valid) {
                    this.delayApplyObject.applyId = this.user.id
                    const res = await saveApply(this.delayApplyObject)
                    if (res.code === 200) {
                        this.$message.success(res.data)
                        this.delayApplyObject = {}
                        this.applyDelayVisible = false
                        this.getTaskByUserId()
                    } else
                        this.$message.error(res.msg)
                }
            })
        },
        //
        async tryFinish(row) {
            // 
            // 
            // 
            // 
            // 
            if (row.type === 0) {
                const res = await countUser(row.caseSubId)
                if (res.code === 200) {
                    //
                    if (res.data.length > 1) {
                        //
                        this.openUpdateDescription(res.data)
                    } else {
                        this.openSelectFinishTime(row)
                    }
                }
            } else {
                this.openSelectFinishTime(row)
            }
        },
        //
        openUpdateDescription(info) {
            this.updateDescriptionVisible = true
            this.directors = info
        },
        //
        openSelectFinishTime(row) {
            this.curTask = { ...row }
            this.selectFinishTimeVisible = true
        },
        //
        changeTime() {
            this.$forceUpdate()
        },
        //
        async submitFinish() {
            if (this.curTask.applyTime < this.curTask.startTime) {
                this.$message.error("")
                return
            }
            this.curTask.applyId = this.user.id
            this.curTask.applyTime = format4back(this.curTask.applyTime)
            const res = await saveFinishApply(this.curTask)
            if (res.code === 200) {
                this.$message.success(res.data)
                this.selectFinishTimeVisible = false
                this.getTaskByUserId()
            } else
                this.$message.error(res.msg)
        },
        //
        async submitDirectorJobDescription() {
            for (var i = 0; i < this.directors.length; i++) {
                if (this.directors[i].userId === this.user.id && (this.directors[i].description === null || this.directors[i].description === '')) {
                    this.$message.error("")
                    return
                }
                if (!('description' in this.directors[i]) || this.directors[i].description === null || this.directors[i].description === '') {
                    // 
                    const res = await updateDescription(this.directors)
                    if (res.code === 200) {
                        this.$message.success(res.data)
                        this.updateDescriptionVisible = false
                    } else {
                        this.$message.error(res.error)
                    }
                    return
                }
            }
            // 
            const res = await updateDescription(this.directors)
            if (res.code === 200) {
                this.$message.success(res.data)
                this.updateDescriptionVisible = false
                setTimeout(() => this.openSelectFinishTime({ caseSubId: this.directors[0].caseSubId }), 200)

            } else {
                this.$message.error(res.error)
            }
        },
        async getUnfinishedCaseList() {
            const res = await unFinishedCaseList()
            if (res.code === 200) {
                this.unFinishedCaseList = res.data
            } else {
                this.$message.error(res.msg)
                return
            }
        },
        initApplyCaseSub() {
            this.applyCaseSubVisible = true
        },
        //
        async getUnfinishedSubList() {
            this.applyCaseSubUser.caseSubId = null
            const res = await unfinishedSubList({ caseId: this.applyCaseSubUser.caseId, userId: this.user.id })
            if (res.code === 200) {
                this.unfinishedSubList = res.data
            } else {
                this.$message.error(res.msg)
                return
            }
        },
        // 
        flushCaseSubId() {
            this.$forceUpdate()
        },
        //
        submitApplyCaseSub() {
            this.$refs.applyCaseSubFormRef.validate(async (valid) => {
                if (valid) {
                    this.applyCaseSubUser.applyId = this.user.id
                    const res = await saveApplyCaseSub(this.applyCaseSubUser)
                    if (res.code === 200) {
                        this.$message.success(res.data)
                        this.applyCaseSubVisible = false
                    } else {
                        this.$message.error(res.msg)
                    }
                }
            })
        },
        submitApplyTask() {
            this.$refs.applyTaskFormRef.validate(async (valid) => {
                if (valid) {
                    this.applyTask.applyId = this.user.id
                    const res = await saveApplyTask(this.applyTask)
                    if (res.code === 200) {
                        this.applyTaskVisible = false
                        this.$message.success(res.data)
                    } else {
                        this.$message.error(res.msg)
                    }
                }
            })

        },
        // //
        // async initPie() {
        //     const res = await recentTaskList(this.user.id)
        //     var typePie = this.$echarts.init(document.getElementById('taskType'))
        //     typePie.setOption({
        //         legend: {
        //             orient: 'vertical',
        //             right: 0,
        //             top: 'top'
        //         },
        //         tooltip: {
        //             trigger: 'item',
        //             formatter: '<h4>{b}:{c}</h4><br><h4>:{d}%<h4>'
