        openCaseDetail(row) {
            this.$router.push({
                name: '',
                query: {
                    caseId: row.caseId,
                    caseName: row.caseName,
                }
            })
        },
        // 
        tryStart(row) {
            this.curInterruptTask = { ...row }
            this.selectStartTimeVisible = true
        },
        //
        async launch() {
            const res = await startOrFinish(this.curInterruptTask)
            if (res.code === 200) {
                this.$message.success(res.data)
                //
                this.getTaskByUserId()
                this.getExceptionList()
                this.selectStartTimeVisible = false
            }
        },
        // 
        computePresetTime() {
            if (this.delayApplyObject.applyDays === '') {
                this.delayApplyObject.presetFinishTime = null
                return
            }
            // 
            this.delayApplyObject.presetFinishTime = formatDate(timeAdd(this.delayApplyObject.startTime, this.delayApplyObject.planDays, this.delayApplyObject.unforcedDays, this.delayApplyObject.applyDelay, this.delayApplyObject.applyDays))
        },
        // 
        initGanttObj(obj, stack, start, color, zlevel, name) {
            obj.name = name
            obj.stack = stack
            obj.type = "bar";
            obj.label = {
                show: true,
                color: "#000",
                position: "right",
                fontSize: 20,
                formatter: function (params) {
                    var data = new Date(params.value)
                    if (zlevel === 1 || zlevel === 2)
                        return data.getMonth() + 1 + "/" + data.getDate()
                    return ""
                }

            }
            obj.zlevel = zlevel
            if (start) {
                obj.itemStyle = {

                    color: "#fff"

                }
            } else {
                obj.itemStyle =
                {
                    borderRadius:5,
                    color: color,
                    borderColor: "#fff",
                    borderWidth: 2,

                }
            }
            obj.data = []
            return obj
        },
        // 
        initGantt() {
            this.gantt = this.$echarts.init(document.getElementById('ganttChart'))
            // y
            var yAxis = [];
            for (let i = this.userInfo.length - 1; i >= 0; i--) {
                yAxis.push(this.userInfo[i].description)
            }
            var dataSeries = []
            // 
            var planTime_start_0 = {};
            var planTime_start_1 = {};
            var planTime_end = {};
            planTime_start_0 = this.initGanttObj(planTime_start_0, "bar0", true, "#6ED77E", 4, "")
            planTime_start_1 = this.initGanttObj(planTime_start_1, "bar1", true, "#6ED77E", 4, "")
            planTime_end = this.initGanttObj(planTime_end, "bar0", false, "#6ED77E", 3, "")

            // 
            var standardTime = {};
            standardTime = this.initGanttObj(standardTime, "bar0", false, "skyblue", 2, "")

            // 
            var execTime_end = {};
            execTime_end = this.initGanttObj(execTime_end, "bar1", false, "#E23D3D", 1, "")

            for (let i = this.userInfo.length - 1; i >= 0; i--) {
                planTime_start_0.data.push(new Date(this.userInfo[i].startTime))
                planTime_start_1.data.push(new Date(this.userInfo[i].startTime))
                planTime_end.data.push(new Date(timeAdd(this.userInfo[i].startTime, this.userInfo[i].planDays)))
                standardTime.data.push(new Date(timeAdd(this.userInfo[i].startTime, this.userInfo[i].planDays, this.userInfo[i].unforcedDays)))
                execTime_end.data.push(new Date(timeAdd(this.userInfo[i].startTime, this.userInfo[i].executionDays)))
            }


            dataSeries.push(planTime_start_0)
            dataSeries.push(planTime_start_1)
            dataSeries.push(planTime_end)
            dataSeries.push(standardTime)
            dataSeries.push(execTime_end)

            var option = {
                backgroundColor: "#fff",
                title: {
                    text: "",
                    padding: 20,
                    textStyle: {
                        fontSize: 17,
                        fontWeight: "bolder",
                        color: "#333"
                    },
                    subtextStyle: {
                        fontSize: 13,
                        fontWeight: "bolder"
                    }
                },
                legend: {
                    data: ['', '', '', ''],
                    align: "right",
                    right: 80,
                    top: 50
                },
                grid: {
                    containLabel: true,
                    show: false,
                    right: 130,
                    left: 40,
                    bottom: 40,
                    top: 90
                },
                xAxis: {
                    type: "time",
                    axisLabel: {
                        "show": true,
                        "interval": 0
                    }
                },
                yAxis: {
                    axisLabel: {
                        show: true,
                        interval: 0,
                    },
                    data: yAxis
                },
                tooltip: {
                    trigger: "axis",
                    formatter: function (params) {
                        var res = ''
                        var set = new Set()
                        res += params[0].axisValue + '<br/>';
                        for (var i = 0; i < params.length; i++) {
                            if (set.has(params[i].seriesName))
                                continue
                            set.add(params[i].seriesName)
                            res += '<div style="display:inline-block;margin-right:5px;border-radius:50%;width:10px;height:10px;background-color:' + params[i].color + ';"></div>' +
                                params[i].seriesName + '' + formatDate(params[i].value) + '<br/>'
                        }
                        return res
                    }
                },
                series: dataSeries,
            }
            this.gantt.setOption(option);
        },
        // 
        handleDoubleClick(row, column) {
            if (column.label === "")
                this.navigateToDetailPage(row)
            else if (column.label === "")
                this.showDelay(row, "")
            else if (column.label === "")
                this.showDelay(row, "")
        },
        // ()
        navigateToDetailPage(row) {
            if (row.caseId !== null) {
                this.$router.push({
                    name: '',
                    query: {
                        caseId: row.caseId,
                        caseName: row.description.split("")[0]
                    }
                })
            }
        },
        // 
        async showDelay(row, delayType) {
            var res = await getDelayById({ caseSubId: row.caseSubId, taskId: row.taskId, delayType: delayType })
            this.delayList = res.data
            this.delayDrawer = true
        },
        // 
        openPause(row) {
            this.pauseObj.taskId = row.taskId
            this.pauseObj.caseSubId = row.caseSubId
            this.pauseObj.target = row.description
            this.pauseVisible = true
        },
        // 
        async startPause() {
            if (this.pauseObj.description === null || this.pauseObj.description === '') {
                this.$message.error("")
                return
            }
            const res = await startPause(this.pauseObj)
            if (res.code === 200) {
                this.$message.success(res.data)
                this.pauseObj.description = ''
                this.pauseObj.taskId = null
                this.pauseObj.caseSubId = null
                this.pauseObj.target = null
                this.pauseVisible = false
            }
            this.getTaskByUserId()
        },
        // 
        finishPause(pauseId) {
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {
                const res = await finishPause(pauseId)
                if (res.code === 200) {
                    this.$message.success(res.data)
                    this.getTaskByUserId()
                }
            })
        }
    }
}
