<template>
    <div shadow="always" class="singleBox"
        :class="{ 'normal': leftDay >= 0, 'appliedDelay': leftDay < 0 && leftDelay >= 0, 'delay': leftDay < 0 && leftDelay < 0 }">


        <div class="info_area">
            <div class="caseName">{{ data.caseName }}</div>
            <div class="subName">{{ data.subName }}</div>
            <el-tooltip class="item" effect="dark" content="" placement="left">
                <el-button size="mini" type="warning" icon="el-icon-timer" round @click="openDelayApply(data)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="left">
                <el-button size="mini" type="success" icon="el-icon-success" round @click="finishCaseSub(data)"></el-button>
            </el-tooltip>
        </div>

        <div class="chart_area">
            <el-progress type="circle" text-color="#ffffff" :stroke-width="10" :percentage="leftRate" :color="showColor"
                :format="showText"></el-progress>
        </div>



        <!--  -->
        <el-dialog title="" :visible.sync="isDatePickerVisible">
            <el-date-picker v-model="selectedFinishDate" type="date" placeholder=""
                value-format="yyyy-MM-dd HH:mm:ss" :picker-options="pickerOptions"></el-date-picker>
            <div slot="footer" class="dialog-footer">
                <el-button @click="isDatePickerVisible = false"> </el-button>
                <el-button type="primary" @click="confirmFinishDate"> </el-button>
            </div>
        </el-dialog>

    </div>
</template>

<script>
import { checkResult, formatDate, timeSub } from '@/utils/common'
import { countUser } from '@/api/caseSubUser'
import { mapState } from 'vuex'
import { saveFinishApply } from '@/api/caseFinishApply'
export default {
    props: {
        data: Object
    },
    data() {
        return {
            leftDay: 0,
            leftRate: 0,
            leftDelay: 0,
            isDatePickerVisible: false,
            selectedFinishDate: "",
            pickerOptions: {
                disabledDate(time) {
                    return time.getTime() > Date.now();
                }
            }
        }
    },
    computed: {
        ...mapState(['user'])
    },
    created() {
        setTimeout(this.getPercentage(), 2000)
    },
    methods: {
        getPercentage() {
            //
            var today = new Date()
            today.setHours(0, 0, 0, 0)
            //
            if (today <= new Date(this.data.presetTime)) {
                //
                var costDay = timeSub(today, this.data.presetTime)
                this.leftDay = costDay
                // 
                const totolDay = timeSub(this.data.startTime, this.data.presetTime)
                this.leftRate = Math.ceil(this.leftDay / totolDay * 100)
            } else {
                //
                var delayDay = timeSub(this.data.presetTime, today)
                this.leftDay = -delayDay
                //
                this.leftDelay = this.data.applyDelay - delayDay
                //
                if (this.leftDelay >= 0) {
                    this.leftRate = (this.leftDelay / this.data.applyDelay) * 100
                } else
                    this.leftRate = (delayDay / this.data.planDays) * 100
            }
            if (this.leftRate > 100)
                this.leftRate = 100
            return this.leftRate
        },
        showText() {
            if (this.leftDay >= 0)
                return `${this.leftDay}`
            else {
                if (this.leftDelay < 0)
                    return `${Math.abs(this.leftDay)}`
                else
                    return `${Math.abs(this.leftDelay)}`
            }
        },
        //
        showColor() {
            if (this.leftDay >= 0)
                return "#67c23a"
            else {
                return "#DFF144"
            }
        },
        //
        openDelayApply(caseSub) {
            this.$router.push({
                path: '/delayApply',
                query: {
                    caseSubId: caseSub.id,
                    caseSubName: caseSub.subName,
                    caseName: caseSub.caseName,
                    chargeId: [this.user.id]
                }
            })
        },
        finishCaseSub(caseSub) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(async () => {
                //
                var res = await countUser(caseSub.id)
                if (res.data.length > 1) {
                    var htmlStr = "<h5>, ?</h5><br><br>"
                    for (var i = 0; i < res.data.length; i++) {
                        htmlStr += `<h5><input type="checkBox">  ${res.data[i]}</h5><br>`
                    }
                    this.$confirm(htmlStr, '', {
                        dangerouslyUseHTMLString: true,
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(async () => {
                        this.applyFinish(caseSub)
                    })
                } else {
                    this.applyFinish(caseSub)
                }
            })
        },
        async applyFinish(caseSub) {
            //,id
            this.isDatePickerVisible = caseSub.id
        },
        async confirmFinishDate() {
            if (this.selectedFinishDate) {
                //  formattedFinishDate 
                var result = await saveFinishApply({
                    caseSubId: this.isDatePickerVisible,
                    applyId: this.user.id,
                    createTime: this.selectedFinishDate // 
                });
                checkResult(result)
                // 
                this.isDatePickerVisible = false;
            } else {
                // 
                this.$message.error("");
            }
        },
    }
}
</script>

<style lang="less" scoped>
.normal {
    background-color: #A4D8DD;
}

.delay {
    background-color: #EE8192;
    ;
}

.appliedDelay {
    background-color: #7F3393;
}

.singleBox {
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: nowrap;
    padding: 10px;
    margin-right: 10px;

    >.el-button{
        margin:0;
        padding: 0;
        box-sizing: border-box;
    }

    >.info_area {
        color:#000;
        > .item{
            display: block;
        }
        display: flex;
        flex-direction: column;
        margin-right: 10px;
        justify-content: space-between;
        align-items: normal;
    }
}
</style>