<template>
  <div class="index_container">
    <!--  -->
    <div>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div>
      <!--  -->
      <div class="userHeader">
        <el-row :gutter="20">
          <el-col :span="4">
            <el-input v-model="caseName" placeholder=""></el-input>
          </el-col>
          <el-col :span="2">
            <el-button type="primary" icon="el-icon-search" @click="jump2QueryCase"></el-button>
          </el-col>
          <!--  -->
          <el-col :span="1" :offset="15">
            <el-badge :value="logList.length" :hidden="logList.length === 0" class="item">
              <el-button round @click="$router.push('/user/info')"><i class="el-icon-chat-dot-round"></i></el-button>
            </el-badge>
          </el-col>
          <el-col :span="1">
            <el-dropdown @command="handleCommand">
              <el-button round>
                <i class="el-icon-setting el-icon-arrow-down"></i>
              </el-button>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="editUser"></el-dropdown-item>
                <el-dropdown-item command="password"></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </el-col>

        </el-row>
      </div>

      <!--  -->
      <div class="notice-container">
        <el-card>
          <h2 style="margin-bottom: 10px;"></h2>
          <div class="link-container">
            <el-link type="primary" target="_blank" href="https://scmail.ude-corp.com/"><i
                class="el-icon-s-promotion"></i></el-link>
            <el-button type="primary" @click="jump2Case"><i class="el-icon-plus"></i></el-button>
            <el-button type="primary" @click="jump2Task"><i class="el-icon-plus"></i></el-button>
            <el-button type="primary" @click="jump2CaseIndex"><i class="el-icon-view"></i></el-button>
            <el-button type="info"></el-button>
          </div>
        </el-card>
      </div>

      <!--  -->
      <div class="task-container">
        <el-card class="taskTable">
          <h2></h2>
          <el-table :data="taskList" style="margin-bottom: 15px;">
            <el-table-column>
              <template slot-scope="scope">
                <el-tag effect="dark" type="success" v-if="scope.row.type === 0"></el-tag>
                <el-tag effect="dark" type="info" v-if="scope.row.type === 1"></el-tag>
                <el-tag effect="dark" v-if="scope.row.type === 2"></el-tag>
              </template>
            </el-table-column>
            <el-table-column prop="caseName"></el-table-column>
            <el-table-column prop="subName"></el-table-column>
            <el-table-column prop="comment"></el-table-column>
            <el-table-column>
              <template slot-scope="scope">
                <!--  -->
                <el-progress v-if="scope.row.startTime === null" :stroke-width="24" :percentage="100" color="#30E0D4"
                  :show-text="false">
                </el-progress>
                <!--  -->
                <el-progress v-else-if="!scope.row.pausing" :stroke-width="24" :percentage="scope.row.percentage"
                  :status="scope.row.finishedOwnWork ? 'primary' : 'leftDelay' in scope.row ? scope.row.leftDelay >= 0 ? 'warning' : 'exception' : 'success'">
                </el-progress>
                <!--  -->
                <el-progress v-else-if="scope.row.pausing" :stroke-width="24" :percentage="scope.row.percentage" color="#909399" :show-text="false">
                </el-progress>
              </template>
            </el-table-column>
          </el-table>


          <!-- <el-row>
            <el-col :span="3" :offset="10">
              <el-button type="primary"></el-button>
            </el-col>
          </el-row> -->
        </el-card>

        <!--  -->
        <el-calendar style="width: 50%;">
          <template slot="dateCell" slot-scope="{data}">

            <div style="width: 100%;height: 100%;font-size: 25px; text-align: center;line-height: 70px;"
              :style="{ 'background-color': isFinishDay(data.day) ? 'skyblue' : isSunDay(data.day) ? '#5BEE58' : 'white' }">
              <el-tooltip v-if="isFinishDay(data.day)" :content="isFinishDay(data.day)" placement="top">
                <template #content>
                  <div style="white-space: pre-wrap;">{{ isFinishDay(data.day) }}</div>
                </template>
                <p style="color:#F13E32">{{ formatData(data.day) + '' }}</p>
              </el-tooltip>
              <el-tooltip v-else-if="isToday(data.day)" content="!" placement="top">
                <p>{{ formatData(data.day) + '' }}</p>
              </el-tooltip>

              <p v-else>
                {{ formatData(data.day) }}
              </p>
            </div>
          </template>
        </el-calendar>
      </div>



      <!--  -->
      <div class="applyArea">
        <caseStatus></caseStatus>
      </div>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="editUserVisible" width="30%">
      <el-form ref="editUserRef" :rules="editUserRule" :model="editUser" label-width="80px">
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="" prop="name">
              <el-input v-model="editUser.name"></el-input>
            </el-form-item>
          </el-col>

          <el-col :span="12">
            <el-form-item label="" prop="number">
              <el-input v-model="editUser.number"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item label="" prop="status">
          <el-select v-model="editUser.status" placeholder="">
            <el-option v-for="item in [{ label: '', value: 0 }, { label: '', value: 1 }]" :key="item.value"
              :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="email">
          <el-input v-model="editUser.email" placeholder=""></el-input>
        </el-form-item>

      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editUserVisible = false"> </el-button>
        <el-button type="primary" @click="handleEditUser()"> </el-button>
      </span>
    </el-dialog>


    <!--  -->
    <el-dialog title="" :visible.sync="editPasswordVisible" width="30%">
      <el-form ref="editPasswordRef" :model="passwordInfo" :rules="passwordInfoRule" label-width="80px">
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="passwordInfo.password"></el-input>
        </el-form-item>


        <el-form-item label="" prop="doublecheck">
          <el-input type="password" v-model="passwordInfo.doublecheck"></el-input>
        </el-form-item>


      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editPasswordVisible = false"> </el-button>
        <el-button type="primary" @click="updatePassword()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import { taskList, notStartTaskList } from '@/api/task'
import { timeSub, timeAdd, formatDate } from '@/utils/common'
import { updatePassword } from '@/api/user'
export default {
  data() {
    var checkNumber = (rule, value, callback) => {
      if (!/^\d{6}$/.test(value)) {
        return callback(new Error('6'))
      }
      callback()
    }
    var checkPassword = (rule, value, callback) => {
      if (!/^(?=.*[a-zA-Z])(?=.*\d).{8,}$/.test(value)) {
        return callback(new Error('8'))
      }
      callback()
    }

    var checkSecond = (rule, value, callback) => {
      if (value !== this.passwordInfo.password) {
        return callback(new Error(''))
      }
      callback()
    }

    var checkEmail = (rule, value, callback) => {
      //
      if (/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(value)) {
        callback()
      }
    }

    return {
      // 
      taskList: [],
      pieChart: null,
      statusPie: null,
      editUserVisible: false,
      editPasswordVisible: false,
      editUser: {},
      editUserRule: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 10, message: '2-10', trigger: 'blur' }
        ],
        number: [
          { validator: checkNumber, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ]
      },
      passwordInfo: {
        password: '',
        doublecheck: ''
      },
      passwordInfoRule: {
        password: [
          { validator: checkPassword, trigger: 'blur' }
        ],
        doublecheck: [
          { validator: checkSecond, trigger: 'blur' }
        ]
      },
      caseName: '',
      today: new Date(),

    }
  },
  computed: {
    ...mapState(['user']),
    ...mapState('log', ['logList'])
  },
  created() {

  },
  async mounted() {
    // 
    await this.initTaskList()
    await this.getNotStartTaskList()
    // this.typePie = this.$echarts.init(document.getElementById('typePie'));
    // this.statusPie = this.$echarts.init(document.getElementById('statusPie'));

    // this.renderPieChart()
  },
  methods: {
    ...mapActions(['editUserInfo']),
    async getNotStartTaskList() {
      const res = await notStartTaskList(this.user.id)
      res.data.forEach(item=>item.comment='')
      this.taskList.push(...res.data)
    },
    async initTaskList() {
      const res = await taskList(this.user.id)
      if (res.code === 200) {
        this.taskList = res.data
        var delayCount = 0
        for (var i = 0; i < this.taskList.length; i++) {
          this.taskList[i].executionDays = timeSub(this.taskList[i].startTime, new Date())
          var presetTime = new Date(this.taskList[i].startTime)
          presetTime = presetTime.setDate(presetTime.getDate()+this.taskList[i].planDays+this.taskList[i].unforcedDays)
          this.taskList[i].presetTime = presetTime
          //
          var today = new Date()
          today.setHours(0, 0, 0, 0)
          // 
          this.taskList[i].pauseStart = this.taskList[i].pauseStart ? formatDate(this.taskList[i].pauseStart) : null
          //
          if (today <= this.taskList[i].presetTime) {
            //
            var costDay = timeSub(this.taskList[i].startTime, today)
            // /
            this.taskList[i].percentage = costDay * 1.0 / (this.taskList[i].planDays + +this.taskList[i].unforcedDays) * 100
          } else {
            //(-1)
            var delayDay = timeSub(this.taskList[i].presetTime, today) - 1
            //
            this.taskList[i].leftDelay = this.taskList[i].applyDelay - delayDay
            //
            if (this.taskList[i].leftDelay >= 0) {
              this.taskList[i].percentage = (delayDay / this.taskList[i].applyDelay) * 100
            } else {
              this.taskList[i].percentage = timeSub(this.taskList[i].startTime, today)*100.0 / this.taskList[i].planDays
              if(!this.taskList[i].finishedOwnWork)
                delayCount++
            }
          }
          this.taskList[i].presetTime = formatDate(this.taskList[i].presetTime)
          this.taskList[i].comment = this.percentageText(this.taskList[i])
          if (this.taskList[i].percentage > 100)
            this.taskList[i].percentage = 100
        }
        if (delayCount) {
          this.$notify({
            title: '',
            message: '' + delayCount + '',
            duration: 0,
            type: 'error'
          });
        }
      } else {
        this.$message.error(res.msg)
      }
    },
    // 
    percentageText(row) {
      if (row.pausing)
        return ""
      if ('leftDelay' in row) {
        if (row.leftDelay >= 0) {
          return `${row.leftDelay}`
        }
        return `${row.applyDelay - row.leftDelay}`
      }
      //+1
      return `${row.planDays + +row.unforcedDays + 1 - row.executionDays}`
    },
    //
    renderPieChart() {
      var typeData = [
        {
          name: '',
          value: 0
        }, {
          name: '',
          value: 0
        }, {
          name: '', value: 0
        }]

      var statusData = [
        {
          name: '',
          value: 0
        },
        {
          name: '',
          value: 0
        },
        {
          name: '',
          value: 0
        }
      ]
      this.taskList.forEach(s => {
        typeData[s.type].value += 1
        if (+s.percentage === 100)
          statusData[0].value += 1
        else if (+s.percentage >= 60)
          statusData[1].value += 1
        else
          statusData[2].value += 1
      })
      // 
      const typeOption = {
        legend: {
          orient: 'vertical',
          x: 'left',
          data: ['', '', '']
        },
        series: [
          {
            type: 'pie',
            data: typeData,
            radius: ['40%', '70%'],
            avoidLabelOverlap: false,
            label: {
              show: false,
              position: 'center'
            },
            itemStyle: {
              color: function (params) {
                if (params.data.name === "")
                  return '#67c23a'
                else if (params.data.name === "")
                  return '#e6a23c'
                else if (params.data.name === "")
                  return '#409eff'
              }
            },
            labelLine: {
              show: false
            },
            emphasis: {
              label: {
                show: true,
                fontSize: '30',
                fontWeight: 'bold'
              }
            },
          }
        ]
      };
      // 
      const statusOption = {
        legend: {
          orient: 'vertical',
          x: 'left',
          data: ['', '', '']
        },
        series: [
          {
            type: 'pie',
            data: statusData,
            radius: ['40%', '70%'],
            avoidLabelOverlap: false,
            label: {
              show: false,
              position: 'center'
            },
            itemStyle: {
              color: function (params) {
                if (params.data.name === "")
                  return '#f56c6c'
                else if (params.data.name === "")
                  return '#e6a23c'
                else if (params.data.name === "")
                  return '#67c23a'
              }
            },
            labelLine: {
              show: false
            },
            emphasis: {
              label: {
                show: true,
                fontSize: '30',
                fontWeight: 'bold'
              }
            },
          }
        ]
      };
      this.typePie.setOption(typeOption);
      this.statusPie.setOption(statusOption);
    },
    //
    handleCommand(command) {
      if (command === 'editUser') {
        //
        this.editUser = { ...this.user }
        this.editUserVisible = true
      } else {
        this.editPasswordVisible = true
      }
    },
    handleEditUser() {
      this.$refs.editUserRef.validate(async (valid) => {
        if (valid) {
          const res = await this.editUserInfo(this.editUser)
          if (res.code === 200) {
            this.$message.success(res.data)
            this.editUserVisible = false
          } else {
            this.$message.error(res.msg)
          }
        }
      })

    },
    //
    updatePassword() {
      this.$refs.editPasswordRef.validate(async (valid) => {
        if (valid) {
          const res = await updatePassword({ password: this.passwordInfo.password, id: this.user.id })
          if (res.code === 200) {
            this.$message.success(res.data)
            this.editPasswordVisible = false
          }
        }
      })
    },
    jump2Case() {
      this.$router.push({
        path: '/user/progress',
        query: {
          applyCaseSubVisible: true
        }
      })
    },
    jump2Task() {
      this.$router.push({
        path: '/user/progress',
        query: {
          applyTaskVisible: true
        }
      })
    },
    jump2CaseIndex() {
      this.$router.push({
        path: '/common/index',
      })
    },
    jump2QueryCase() {
      this.$router.push({
        path: '/common/index',
        query: {
          caseName: this.caseName
        }
      })
    },
    isSunDay(date) {
      date = new Date(date)
      return date.getDay() === 0
    },
    formatData(date) {
      var array = date.split("-")
      if (+array[2] === 1)
        return array[1] + ""
      else
        return +array[2]
    },
    isToday(date) {
      const today = new Date().toISOString().slice(0, 10);
      return date === today
    },
    isFinishDay(date) {
      var str = ''
      for (let i = 0; i < this.taskList.length; i++) {
        if (timeAdd(this.taskList[i].presetTime, +this.taskList[i].applyDelay) === date) {
          str += this.taskList[i].description+"\n"
        }
      }
      return str===''?false:str
    }
  },


}
</script>

<style lang="less" scoped>
.el-button {
  border: none;
}

.applyArea {
  margin-top: 30px;
  margin-bottom: 30px;
  width: 100%;
}

.task-container {
  display: flex;
  justify-content: space-between;
}

.taskTable {
  width: 50%;
  margin-right: 10px;
}

.taskCharts {
  width: 50%;
}

.notice-container {
  margin-top: 20px;

  .link-container {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    margin: 0 30px;
  }

  .el-button {
    font-size: 15px;
  }
}
</style>