<template>
    <div>
        <!--  -->
        <div>
            <el-breadcrumb separator="/">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <el-card v-if="checkCaseSubHistoryVisible === false">
            <div class="btns">
                <el-row>
                    <el-col :span="5" :offset="9">
                        <h1></h1>
                    </el-col>
                    <el-col :span="2" :offset="8">
                        <el-button type="info" icon="el-icon-s-order" @click="openHistory()">  </el-button>
                    </el-col>
                </el-row>
            </div>
            <el-table :data="caseSubList">
                <el-table-column prop="caseName" label=""></el-table-column>
                <el-table-column prop="subName" label=""></el-table-column>
                <el-table-column prop="description" label=""></el-table-column>
                <el-table-column prop="applyName" label=""></el-table-column>
                <el-table-column prop="createTime" label=""></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="success" @click="judgeApply(scope.row, 1)"></el-button>
                        <el-button type="primary" @click="judgeApply(scope.row, 2)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <!--  -->
        <el-card v-else-if="checkCaseSubHistoryVisible">
            <el-page-header @back="checkCaseSubHistoryVisible = false" content=""></el-page-header>
            <el-table :data="applyHistoryList" style="width: 100%">
                <el-table-column prop="caseName" label="">
                </el-table-column>
                <el-table-column prop="subName" label="">
                </el-table-column>
                <el-table-column prop="applyName" label="">
                </el-table-column>
                <el-table-column prop="createTime" label="">
                </el-table-column>
                <el-table-column prop="checkName" label="">
                </el-table-column>
                <el-table-column prop="checkTime" label="">
                </el-table-column>
                <el-table-column prop="rejectReason" label="">
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tag effect="dark" type="success" v-if="scope.row.status === 1"></el-tag>
                        <el-tag effect="dark" type="danger" v-else-if="scope.row.status === 2"></el-tag>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination style="margin-top: 10px;" @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page="queryInfo.page" :page-sizes="[5, 8, 10, 15]" :page-size="queryInfo.pageSize"
                layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
import { judgeApply, applyHistoryList } from '@/api/applyCaseSub'
import { mapActions, mapState } from 'vuex'
export default {
    data() {
        return {
            applyList: [],
            checkCaseSubHistoryVisible: false,
            applyHistoryList: [],
            queryInfo: {
                page: 1,
                pageSize: 7
            },
            total: 0
        }
    },
    created() {
        var pageSize = +localStorage.getItem("pim_check_apply_caseSub_pageSize")
        this.queryInfo.pageSize = pageSize == 0 ? 7 : pageSize
        this.getCaseSubApplyList()
    },
    computed: {
        ...mapState(['user']),
        ...mapState('apply', ['caseSubList'])
    },
    methods: {
        ...mapActions('apply',['getCaseSubApplyList']),
        judgeApply(row, status) {
            row.status = status
            row.checkUser = this.user.id
            if (status === 1) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(async () => {

                    const res = await judgeApply(row)
                    if (res.code === 200) {
                        this.$message.success(res.data)
                        this.getCaseSubApplyList()
                    } else {
                        this.$message.error(res.msg)
                    }
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            } else if (status === 2) {
                this.$prompt('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                }).then(async ({ value }) => {
                    row.rejectReason = value
                    const res = await judgeApply(row)
                    if (res.code === 200) {
                        this.$message.success(res.data)
                        this.getCaseSubApplyList()
                    } else {
                        this.$message.error(res.msg)
                    }
                }).catch((error) => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            }
        },
        openHistory() {
            this.checkCaseSubHistoryVisible = true
            this.getApplyHistoryList()
        },
        async getApplyHistoryList() {
            const res = await applyHistoryList(this.queryInfo)
            if (res.code === 200) {
                this.applyHistoryList = res.data.records
                this.total = res.data.total
            } else {
                this.$message.error(res.msg)
            }
        },
        handleSizeChange(value) {
            localStorage.setItem("pim_check_apply_caseSub_pageSize", value)
            this.queryInfo.pageSize = value
            this.queryInfo.page = 1
            this.getApplyHistoryList()
        },
        handleCurrentChange(value) {
            this.queryInfo.page = value
            this.getApplyHistoryList()
        }
    }
}
</script>

<style scoped></style>