<template>
    <div>
        <!--  -->
        <div>
            <el-breadcrumb separator="/">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!--  -->
        <el-card>
            <!--  -->
            <div class="query-area">
                <el-row :gutter="10">
                    <el-col :span="4">
                        <el-input placeholder="" v-model="queryInfo.query" @change="searchUser()"></el-input>
                    </el-col>

                    <el-col :span="2">
                        <el-button type="primary" icon="el-icon-search" @click="searchUser()"></el-button>
                    </el-col>

                    <el-col :span="2">
                        <el-button type="primary" icon="el-icon-plus" @click="addFormVisible = true"></el-button>
                    </el-col>

                    <el-col :span="1" :offset="15">
                        <el-dropdown @command="handleCommand">
                            <el-button round>
                                <i class="el-icon-setting el-icon-arrow-down"></i>
                            </el-button>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item command="editUser"></el-dropdown-item>
                                <el-dropdown-item command="password"></el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </el-col>
                </el-row>
            </div>
            <!--  -->
            <el-table :data="userList" border>
                <el-table-column type="index"></el-table-column>
                <el-table-column prop="name" label=""></el-table-column>
                <el-table-column prop="number" label=""></el-table-column>
                <el-table-column prop="status" label="">
                    <template slot-scope="scope">
                        <el-tag v-if="scope.row.type === 1"></el-tag>
                        <el-tag type="success" v-else></el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tag effect="dark" v-if="scope.row.status === 0"></el-tag>
                        <el-tag effect="dark" type="success" v-else-if="scope.row.status === 1"></el-tag>
                        <el-tag effect="dark" type="warning" v-else-if="scope.row.status === 2">IE</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button icon="el-icon-edit" type="primary" round size="mini"
                            @click="openEditUser(scope.row.id)"></el-button>
                        <el-button icon="el-icon-delete" type="danger" round size="mini"
                            @click="removeUser(scope.row.id)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page="queryInfo.page" :page-sizes="[5, 7, 10, 20]" :page-size="queryInfo.pageSize"
                layout="total, sizes, prev, pager, next" :total="total">
            </el-pagination>
        </el-card>

        <!-- dialog -->
        <el-dialog title="" :visible.sync="addFormVisible" width="30%" @close="$refs.addFormRef.resetFields()">
            <el-form ref="addFormRef" :model="addForm" label-width="80px" :rules="addFormRules">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <el-form-item label="" prop="name">
                            <el-input v-model="addForm.name"></el-input>
                        </el-form-item>
                    </el-col>

                    <el-col :span="12">
                        <el-form-item label="" prop="number">
                            <el-input v-model.number="addForm.number"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>

                <el-form-item label="" prop="status">
                    <el-select v-model="addForm.status" placeholder="">
                        <el-option v-for="item in statusOps" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>

            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false"> </el-button>
                <el-button type="primary" @click="addUser()"> </el-button>
            </span>
        </el-dialog>

        <!-- dialog -->
        <el-dialog title="" :visible.sync="editFormVisible" width="30%" @close="$refs.editFormRef.resetFields()">
            <el-form ref="editFormRef" :model="editForm" label-width="80px" :rules="addFormRules">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <el-form-item label="" prop="name">
                            <el-input v-model="editForm.name"></el-input>
                        </el-form-item>
                    </el-col>

                    <el-col :span="12">
                        <el-form-item label="" prop="number">
                            <el-input v-model.number="editForm.number"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>

                <el-form-item label="" prop="status">
                    <el-select v-model="editForm.status" placeholder="">
                        <el-option v-for="item in statusOps" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>

            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editFormVisible = false"> </el-button>
                <el-button type="primary" @click="editUser()"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="editUserVisible" width="30%">
            <el-form ref="editUserRef" :rules="editUserRule" :model="editingUser" label-width="80px">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <el-form-item label="" prop="name">
                            <el-input v-model="editingUser.name"></el-input>
                        </el-form-item>
                    </el-col>

                    <el-col :span="12">
                        <el-form-item label="" prop="number">
                            <el-input v-model="editingUser.number"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>

                <el-form-item label="" prop="status">
                    <el-select v-model="editingUser.status" placeholder="">
                        <el-option v-for="item in [{ label: '', value: 0 }, { label: '', value: 1 }]" :key="item.value"
                            :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="" prop="email">
                    <el-input v-model="editingUser.email" placeholder=""></el-input>
                </el-form-item>

            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editUserVisible = false"> </el-button>
                <el-button type="primary" @click="handleEditUser()"> </el-button>
            </span>
        </el-dialog>


        <!--  -->
        <el-dialog title="" :visible.sync="editPasswordVisible" width="30%">
            <el-form ref="editPasswordRef" :model="passwordInfo" :rules="passwordInfoRule" label-width="80px">
                <el-form-item label="" prop="password">
                    <el-input type="password" v-model="passwordInfo.password"></el-input>
                </el-form-item>


                <el-form-item label="" prop="doublecheck">
                    <el-input type="password" v-model="passwordInfo.doublecheck"></el-input>
                </el-form-item>


            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editPasswordVisible = false"> </el-button>
                <el-button type="primary" @click="updatePassword()"> </el-button>
            </span>
        </el-dialog>

        <el-dialog title="" :visible.sync="chargeCaseSubVisible" width="40%">
            <el-table :data="chargingTask">
                <el-table-column prop="desc" label=""></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tag effect="dark" type="info" v-if="scope.row.startTime === null"></el-tag>
                        <el-tag effect="dark" type="primary" v-else>{{scope.row.startTime}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-select v-model="scope.row.userId" placeholder="">
                            <el-option v-for="item in allUser" :key="item.id" :label="item.name" :value="item.id">
                            </el-option>
                        </el-select>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="danger" icon="el-icon-delete" @click=deleteByChargeId(scope.row.caseSubId,scope.row.userId)></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="updateChargeTask()"> </el-button>
                <el-button @click="chargeCaseSubVisible = false"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { getUserPage, saveUser, getUserById, updateUser, removeUser } from '@/api/user'
import { mapActions, mapState } from 'vuex'
import { updatePassword,getUserListWithAssistants } from '@/api/user'
import { getChargeCaseSub } from '@/api/caseSub'
import {removeDirector,updateChargeCaseSub} from '@/api/caseSubUser'
export default {
    data() {
        var checkNumber = (rule, value, callback) => {
            if (!/^\d{6}$/.test(value))
                return callback(new Error('6'))
            return callback()
        }
        var checkPassword = (rule, value, callback) => {
            if (!/^(?=.*[a-zA-Z])(?=.*\d).{8,}$/.test(value)) {
                return callback(new Error('8'))
            }
            callback()
        }

        var checkSecond = (rule, value, callback) => {
            if (value !== this.passwordInfo.password) {
                return callback(new Error(''))
            }
            callback()
        }

        var checkEmail = (rule, value, callback) => {
            //
            if (/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(value)) {
                callback()
            }
            callback(new Error(''))
        }
        return {
            userList: [],
            total: 0,
            queryInfo: {
                page: 1,
                pageSize: 7,
                query: ''
            },
            //
            addFormVisible: false,
            addForm: {
                name: '',
                number: '',
                status: null
            },
            statusOps: [
                { value: 0, label: "" },
                { value: 1, label: "" },
                { value: 2, label: "IE" },
            ],
            addFormRules: {
                name: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                number: [
                    { required: true, message: '', trigger: 'blur' },
                    { validator: checkNumber, trigger: 'blur' }
                ],
                status: [
                    { required: true, message: '', trigger: 'blur' }
                ],
            },
            //
            editFormVisible: false,
            editForm: {
                id: null,
                name: '',
                number: '',
                status: null,
                email: ''
            },
            //
            editUserVisible: false,
            editPasswordVisible: false,
            // 
            editingUser: {},
            editUserRule: {
                name: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 2, max: 10, message: '2-10', trigger: 'blur' }
                ],
                number: [
                    { validator: checkNumber, trigger: 'blur' }
                ],
                email: [
                    { required: true, message: '', trigger: 'blur' },
                    { validator: checkEmail, trigger: 'blur' }
                ]
            },
            passwordInfo: {
                password: '',
                doublecheck: ''
            },
            passwordInfoRule: {
                password: [
                    { validator: checkPassword, trigger: 'blur' }
                ],
                doublecheck: [
                    { validator: checkSecond, trigger: 'blur' }
                ]
            },
            // 
            chargingTask: [],
            chargeCaseSubVisible: false,
            allUser: []
        }
    },
    created() {
        this.getUserPage()
        this.getAllUser()
    },
    computed: {
        ...mapState(['user'])
    },
    methods: {
        ...mapActions(['editUserInfo']),
        async getAllUser(){
            var res = await getUserListWithAssistants()
            if(res.code===200){
                this.allUser = res.data
            }
        },
        async getUserPage() {
            const res = await getUserPage(this.queryInfo)
            this.userList = res.data.records
            this.total = res.data.total
        },
        handleSizeChange(value) {
            //1
            this.queryInfo.page = 1
            this.queryInfo.pageSize = value
            this.getUserPage()
        },
        handleCurrentChange(value) {
            this.queryInfo.page = value
            this.getUserPage()
        },
        //
        searchUser() {
            //1
            this.queryInfo.page = 1
            this.getUserPage()
        },
        //
        addUser() {
            this.$refs.addFormRef.validate(async valid => {
                if (!valid) return;
                const res = await saveUser(this.addForm)
                if (res.code === 200) {
                    this.$message.success(res.data)
                    this.getUserPage()
                    //
                    setTimeout(() => {
                        this.addFormVisible = false
                    }, 1000);
                } else {
                    this.$message.error(res.msg)
                }
            })
        },
        //
        async openEditUser(id) {
            const res = await getUserById(id)
            if (res.code !== 200)
                return this.$message.error("")
            this.editForm = res.data
            this.editFormVisible = true
        },
        //
        async editUser() {
            const res = await updateUser(this.editForm)
            if (res.code === 200) {
                this.$message.success(res.data)
                this.getUserPage()
                this.editFormVisible = false
            } else {
                this.$message.error(res.msg)
            }
        },
        //
        async removeUser(id) {
            //
            if (this.userList.length === 1)
                this.queryInfo.page -= 1
            const res = await removeUser(id)
            if (res.code === 200) {
                this.$message.success(res.data)
                this.getUserPage()
            } else {
                if (res.msg === "") {
                    setTimeout(async () => {
                        var r = await getChargeCaseSub(id)
                        this.chargingTask = r.data
                        this.chargeCaseSubVisible = true
                    }, 1000)
                }
            }
        },
        //
        handleCommand(command) {
            if (command === 'editUser') {
                //
                this.editingUser = { ...this.user }
                this.editUserVisible = true
            } else {
                this.editPasswordVisible = true
            }
        },
        handleEditUser() {
            this.$refs.editUserRef.validate(async (valid) => {
                if (valid) {
                    const res = await this.editUserInfo(this.editingUser)
                    if (res.code === 200) {
                        this.$message.success(res.data)
                        this.editUserVisible = false
                    } else {
                        this.$message.error(res.msg)
                    }
                }
            })

        },
        //
        updatePassword() {
            this.$refs.editPasswordRef.validate(async (valid) => {
                if (valid) {
                    const res = await updatePassword({ password: this.passwordInfo.password, id: this.user.id })
                    if (res.code === 200) {
                        this.$message.success(res.data)
                        this.editPasswordVisible = false
                    }
                }
            })
        },
        // 
        async deleteByChargeId(caseSubId,userId){
            var res = await removeDirector({caseSubId:caseSubId,userId:userId})
            if(res.code===200){
                this.$message.success(res.data)
                this.chargingTask = this.chargingTask.filter(item=>item.caseSubId!==caseSubId||item.userId!==userId)
            }else{
                this.$message.error(res.msg)
            }
        },
        // 
        async updateChargeTask(){
            var res = await updateChargeCaseSub(this.chargingTask)
            if(res.code===200){
                this.$message.success(res.data)
                this.chargingTask = []
                this.chargeCaseSubVisible = false
            }
        }
    }
}
</script>

<style lang="less" scoped>
.query-area {
    margin-bottom: 10px;
}
</style>