<template>
    <div>
        <!--  -->
        <div>
            <el-breadcrumb separator="/">
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <el-card>


            <el-table :data="demandList">
                <el-table-column label="" prop="description"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tag type="danger" v-if="!scope.row.type">BUG</el-tag>
                        <el-tag type="primary" v-else></el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="createName"></el-table-column>
                <el-table-column label="" prop="createTime"></el-table-column>
                <el-table-column label="" prop="updateTime"></el-table-column>
                <el-table-column label="" v-if="user.name === 'admin'">
                    <template slot-scope="scope">
                        <el-button type="success" @click="finishDemand(scope.row)" v-if="scope.row.updateTime===null">
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <br>
            <el-row>
                <el-col :span="12">
                    <el-input v-model="description" class="textarea-placeholder-multiline" type="textarea" :rows="4"
                        placeholder="/bug,  
: XXXXXX/
:  
: ,XX/"></el-input>
                </el-col>
                <el-col :span="3" :offset="3">
                    <el-select v-model="type" placeholder="">
                        <el-option v-for="item in [{ label: 'bug', value: 0 }, { label: '', value: 1 }]" :key="item.value"
                            :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col :span="2" :offset="4">
                    <el-button type="primary" @click="addDemand"> </el-button>
                </el-col>
            </el-row>
        </el-card>
    </div>
</template>

<script>
import { getDemandList, addDemand,finishDemand } from '@/api/demand'
import { mapState } from 'vuex'
export default {
    data() {
        return {
            description: '',
            type: null,
            demandList: ''
        }
    },
    created() {
        // 
        this.getDemandList()
    },
    computed: {
        ...mapState(['user'])
    },
    methods: {
        async getDemandList() {
            const res = await getDemandList()
            this.demandList = res.data
        },
        async addDemand() {
            if (this.description === '' || this.description.length <= 10) {
                this.$message.error("bug")
                return
            }
            if (this.type === null) {
                this.$message.error("bug")
                return
            }
            const res = await addDemand({ createName: this.user.name, description: this.description, type: this.type })
            this.$message.success(res.data)
            this.description = '',
            this.type = null
            this.getDemandList()
        },
        async finishDemand(row) {
            const res = await finishDemand({ id: row.id })
            this.$message.success(res.data)
            this.getDemandList()
        }
    }
}
</script>

<style lang="less" scoped>
.el-button {
    align-items: center;
}
</style>